.search-modal{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  transition-delay: .3s;
  &._open{
    transition-delay: 0s;
    opacity: 1;
    pointer-events: unset;
    &~.container .burger:before{
      opacity: 0;
    }
  }
  &__content{
    position: relative;
    z-index: 3;
  }
  &__field{
    position: relative;
    height: 5.25em;
    z-index: 3;
    input{
      background-color: #fff;
      border: none;
      border-bottom: $border;
      font-size: 1em;
      height: 100%;
      width: 100%;
      padding-left: 5.5em;
      padding-right: 7em;
      &::placeholder{
        color: #454545;
      }
    }
  }
  &__clear,
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 5.25em;
    width: 1em;
    @include center;
    img{
      width: 100%;
      display: block;
    }
  }
  &__clear{
    display: none;
  }
  &._show-result{
    .search-result{
      transform: translateY(0);
    }
  }
}

.search-result{
  transition: all .5s;
  transform: translateY(-120%);
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  padding: 1.5625em 5.5em 2.5em;
  background-color: $white;
  max-height: calc(100 * var(--vh) - 5.25em);
  overflow-y: auto;
  &__items{
    @include grid100gap(1.25em);
  }
  &__link{
    margin-top: 1.5625em;
    text-transform: unset;
    height: 3em;
    font-weight: 500;
    span{
      font-size: .9375em;
    }
  }
}

.search-item{
  display: flex;
  align-items: center;
  &__img{
    @include imgCover2(100%);
    padding: 0;
    width: 3.75em;
    height: 3.5em;
    margin-right: .625em;
    flex-shrink: 0;
  }
  &__text{
    padding-right: 1em;
    @include grid100gap(.3125em);
  }
  &__link{
    flex-shrink: 0;
    margin-left: auto;
    padding: 0 1.3125em;
    height: 2.25em;
    span{
      font-size: .75em;
    }
  }
}

.filter,
.header-menu{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  opacity: 0;
  pointer-events: none;
  &__bg{
    opacity: .8;
  }
  &__content{
    @include scroll;
    transition: all .4s;
    transform: translateX(-100%);
    height: $vh;
    overflow-y: auto;
    position: relative;
    z-index: 3;
    width: 23.375em;
    background-color: $white;
    padding: 3.125em 1.5em 1.5em 6.25em;
  }
  &__close{
    position: absolute;
    top: .625em;
    right: .625em;
    width: .875em;
    @include center;
    opacity: .4;
    &:hover{
      opacity: 1;
    }
    svg{
      width: 100%;
    }
  }
}

.header-menu{
  transition-delay: .3s;
  &__logo{
    width: 6.25em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    margin-top: 2.5em;
    .menu{
      font-weight: 500;
      text-transform: uppercase;
      &>li{
        padding: 1em 0;
        &>a{
          font-size: .875em;
          display: inline-flex;
          align-items: center;
          position: relative;
          &:after{
            content: '';
            @include centerAbsolute;
            height: 200%;
            width: 120%;
          }
          &:before{
            content: '';
            background-image: url(../img/arrow-right3.svg);
            @include bgImgSize(100%);
            flex-shrink: 0;
            width: 1.65em;
            height: .72em;
            margin-right: .72em;
            transition: all .3s;
          }
          &:not(:hover){
            &:before{
              width: 0;
              margin-right: 0;
            }
          }
        }
      }
      .menu-item-has-children{
        &._active{
          &>a{
            color: $primary;
            &:after{
              transform: rotate(-180deg);
            }
          }
        }
        &>a{
          cursor: pointer;
          &:before{
            display: none;
          }
          &:after{
            content: '';
            flex-shrink: 0;
            width: .71em;
            height: .72em;
            background-image: url(../img/select-arrow.svg);
            @include bgImgSize(100%);
            margin-left: .72em;
            transition: all .3s;
          }
        }
        &>ul{
          display: none;
          padding-left: .9375em;
          padding-top: .9375em;
          margin-bottom: -.625em;
          li{
            margin-bottom: .9375em;
            &:last-child{
              margin-bottom: 0;
              font-weight: 600;
              a{
                border-bottom: 1px solid rgba(0, 0, 0, 0.22);
              }
            }
          }
          a{
            font-size: .75em;
            line-height: 117%;
          }
        }
      }
    }
  }
  .socials{
    margin-top: 2.5em;
  }
}

.empty-search{
  display: flex;
  align-items: center;
  padding: .9375em;
  background-color: #f8f8f8;
  background-image: url(../img/empty-search-bg.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: right top;
  &._inline{
    display: inline-flex;
  }
  &._success{
    color: #52B461;
  }
  &__icon{
    width: 1.75em;
    margin-right: .4375em;
  }
  &__text{
    font-size: .9375em;
    line-height: 160%;
    a{
      color: $primary;
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){
  .search-modal{
    &__field{
      height: 3.375em;
      box-shadow: 0 0 .875em rgba(0, 0, 0, 0.15);
      input{
        font-weight: 500;
        text-transform: uppercase;
        font-size: .6875em;
        padding-left: 2.27em;
        padding-right: 5.5em;
        &::placeholder{
          color: rgba($black,.4);
        }
      }
    }
    &__btn{
      width: 1.5em;
      height: 1.5em;
      right: 3em;
      img{
        width: 1em;
      }
    }
    &__clear{
      width: .875em;
      right: 1.5625em;
      opacity: .4;
      display: flex;
      &:hover{
        opacity: 1;
      }
    }
    &._show-result{
      .search-modal__field{
        box-shadow: unset;
        input{
          border-color: transparent;
        }
      }
    }
  }

  .search-result{
    padding: .25em 1.5625em 3.5em;
    max-height: calc(100 * var(--vh) - 6.75em);
    &__items{
      gap: .5em;
    }
    &__link{
      margin-top: .5em;
    }
  }

  .search-item{
    min-height: 5em;
    &__title{
      font-size: 1em;
    }
    &__link{
      padding: 0 .6875em;
      height: 1.875em;
      span{
        font-size: .625em;
        letter-spacing: .03em;
      }
    }
  }

  .filter,
  .header-menu{
    &__content{
      max-width: 100%;
      width: 20.625em;
    }
  }

  .header-menu{
    &__content{
      padding: 2.1875em;
    }
    .socials,
    &__links{
      margin-top: 1.875em;
    }
    &__links{
      .menu{
        &>li{
          padding: .625em 0;
          &>a{
            font-size: .8125em;
          }
        }
        .menu-item-has-children>ul{
          margin-bottom: -.25em;
          padding-top: .625em;
        }
      }
    }
  }

  .empty-search{
    align-items: flex-start;
    background-image: url(../img/empty-search-bg2.svg);
  }
}


@media screen and (max-width: 576px){
  .search-modal{
    &__field{
      input{
        padding-left: 1.36em;
      }
    }
    &__clear{
      right: .9375em;
    }
    &__btn{
      right: 2.5em;
    }
  }

  .search-result{
    padding: 0 .9375em 1em;
  }

  .search-item{
    min-height: 5em;
    &__title{
      font-size: 1em;
    }
    &__link{
      padding: 0 .6875em;
      height: 1.875em;
      span{
        font-size: .625em;
        letter-spacing: .03em;
      }
    }
  }

  .empty-search{
    background-image: url(../img/empty-search-bg3.svg);
  }
}
