.text{
  ol,
  ul,
  p{
    @include lastM(0 0 1em);
  }
  hr{
    height: 1.5em;
    display: block;
    margin: 0;
    opacity: 0;
  }
  h3{
    margin-bottom: .5em;
  }
  h4{
    font-weight: 500;
    margin-bottom: 1em;
  }
  ul,ol{
    @include grid100gap(1.25em);
    li{
      position: relative;
      padding-left: .9375em;
    }
  }
  ul{
    li{
      &:before{
        content: '';
        position: absolute;
        top: .625em;
        left: 0;
        width: .3125em;
        height: .3125em;
        box-shadow: 0 0 1.5em rgba(255, 96, 1, 0.61);
        background-color: $primary;
      }
    }
  }
  ol{
    counter-reset: section;
    padding-left: .4375em;
    li{
      padding-left: 1.125em;
      counter-increment: section;
      &:before {
        content: counter(section) '. ';
        position: absolute;
        left: 0;
        top: 0;
        width: auto;
        height: auto;
        background-color: transparent;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text {
    .text16-tablet{
      font-size: 1em;
    }
    li,
    p{
      font-size: .875em;
    }
    hr{
      height: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    p{
      font-size: .9375em;
      line-height: 173%;
    }
  }

  .article{
    blockquote{
      padding: 2.875em .6875em .6875em .6875em;
      p{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }
}
