@keyframes pulse {
  0% {
    box-shadow: 0 0 1.5em 0 rgba(255, 96, 1, 0.61);
  }
  70% {
    box-shadow: 0 0 1.5em .625em rgba(255, 96, 1, 0);
  }
  100% {
    box-shadow: 0 0 1.5em 0 rgba(255, 96, 1, 0);
  }
}

.subscribe{
  padding: 8.125em 0;
  &__inner{
    padding: 3.125em;
    background-color: #F8F8F8;
    background-image: url(../img/subscribe-bg.svg);
    background-size: auto 100%;
    background-repeat: no-repeat;
    background-position: right bottom;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: .75em;
      height: .75em;
      background-color: $primary;
      box-shadow: 0 0 1.5em rgba(255, 96, 1, 0.61);
      animation: pulse 2s infinite;
    }
  }
  &__top{
    max-width: 41em;
  }
  &__title{
    font-size: 2em;
    line-height: 134%;
  }
}

.subscribe-form{
  margin-top: 2em;
  &__fields{
    display: grid;
    grid-template-columns: 26.25em 19.625em;
    gap: 1.25em;
  }
  &__row,
  &__bottom{
    display: flex;
  }
  &__btn{
    padding: 0 3em;
    margin: 0 1.875em;
  }
  &__text{
    color: #727272;
    max-width: 30em;
    .text14{
      line-height: 150%;
    }
    a{
      text-decoration: underline;
    }
  }
  &__sex{
    margin-top: 2em;
    .m-radio{
      margin-right: 1.875em;
    }
  }
}

.date-input{
  input{
    cursor: pointer;
    background-image: url(../img/calendar.svg);
    background-repeat: no-repeat;
    background-position: right .625em center;
    background-size: 1.25em auto;
  }
}

.categories{
  padding: 1.25em 0;
  &--new{
    padding: 4em 0 0;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.category-card{
  display: block;
  background-color: #f8f7f8;
  overflow: hidden;
  position: relative;
  &:hover{
    .category-card__img{
      transform: scale(1.1);
    }
    .category-card__icon{
      background-color: $primary;
      path{
        stroke: $white;
      }
    }
  }
  &__img{
    transition: all .6s;
    @include imgCover(110%);
  }
  &__text{
    position: absolute;
    display: flex;
    align-items: center;
    padding: 2.5em;
    left: 0;
    bottom: 0;
  }
  &__title{
    font-size: 1.6875em;
    line-height: 185%;
  }
  &__icon{
    transition: all .3s;
    border-radius: 50%;
    width: 2.25em;
    height: 2.25em;
    @include center;
    margin-left: .9375em;
    flex-shrink: 0;
    svg{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .subscribe{
    padding: 1.875em 0;
    &__inner{
      background-image: url(../img/subscribe-bg2.svg);
      padding: 1.25em 1.5625em;
    }
    &__top{
      max-width: 30em;
    }
    &__title{
      font-size: 1.5em;
    }
  }

  .subscribe-form{
    margin-top: 1.25em;
    &__row{
      display: block;
    }
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    &__bottom{
      margin-top: .9375em;
    }
    &__btn{
      padding: 0 4.375em;
      margin: 0 .9375em 0 0;
    }
    &__text{
      .text14{
        line-height: 138%;
      }
    }
    &__sex{
      margin-top: 1.25em;
      .m-radio{
        margin-right: 1.25em;
      }
    }
  }

  .categories{
    padding: 1.5625em 0;
    &--new{
      padding: 1.875em 0 0;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .category-card{
    &__img{
      padding-bottom: 111%;
    }
    &__text{
      padding: 1.25em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 135%;
    }
  }
}


@media screen and (max-width: 576px){
  .subscribe{
    &__inner{
      background-image: url(../img/subscribe-bg3.svg);
      background-size: auto calc(100% - 3em);
      padding: 1.25em;
    }
    &__title{
      font-size: 1.125em;
      line-height: 133%;
    }
  }

  .subscribe-form{
    &__fields{
      grid-template-columns: 100%;
    }
    &__bottom{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .9375em;
    }
  }

  .categories{
    padding: .9375em 0;
    &--new{
      padding: 1.875em 0 0;
    }
    &--scroll{
      padding: 0;
      .container{
        padding: 0;
      }
      .categories__grid{
        padding: .9375em;
        overflow-x: auto;
        display: flex;
        gap: unset;
      }
      .category-card{
        width: 17.5em;
        flex-shrink: 0;
        margin-right: .625em;
        &:last-child{
          margin-right: 0;
        }
      }
    }
    &--new{
      .category-card__img{
        padding-bottom: 94%;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }

  .category-card{
    &__text{
      padding-right: .75em;
    }
  }
}
