.delivery {
  .m-arrows{
    align-self: flex-end;
  }
  .about-top {
    padding-bottom: 1.875em;
    .delivery__text {
      display: none;
    }
  }
  .m-title--arrow svg {
    top: unset;
    bottom: 0;
    margin-left: 0;
  }
  .about-top__desc {
    max-width: 25.9375em;
    display: flex;
    align-items: flex-end;
    padding: 0 0 2.875em;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75em;
    p {
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 1.5em;
    margin-top: 2.5em;
    &-desc {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.75em;
    }
  }
  &__logo {
    width: 15.3125em;
    height: 7.5em;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.0625em solid #EEEEEE;
    padding: 1em;
    img {
      max-width: 100%;
    }
  }
  &__subtitle {
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 500;
  }
  &__info {
    display: inline-flex;
    gap: 0.75em;
    background: #F8F8F8;
    padding: 0.875em;
    margin: 2.5em 0 0.875em 0;
    i {
      width: 1.5em;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__group2 {
    margin-top: 2.5em;
  }
  &__items {
    max-width: 38.75em;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    gap: 1.25em;
    margin-top: 1.875em;
  }
  &__item {
    display: flex;
    flex-direction: column;
    border: 0.1875em solid #EEEEEE;
    padding: 2em;
    &-title {
      font-size: 1.125em;
      line-height: 130%;
      font-weight: 600;
      text-transform: uppercase;
      margin-bottom: auto;
    }
    &-text {
      margin-top: 2.5em;
      p {
        font-size: 0.75em;
        line-height: 160%;
        text-transform: uppercase;
        color: #8C8C8C;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .delivery {
    .about-top {
      .delivery__text {
        display: block;
        p br {
          display: none;
        }
      }
    }
    .about-top__desc {
      padding: 0;
    }
    .remove-text {
      display: none;
    }
    &__text {
      p {
        font-size: 0.9375em;
      }
    }
    &__group {
      gap: 1.25em;
      margin-top: 0;
    }
    &__info {
      margin-top: 1.875em;
      i {
        margin-top: -0.125em;
      }
      p {
        font-size: 0.875em;
      }
    }
    &__group2 {
      margin-top: 1.875em;
    }
    &__items {
      max-width: 100%;
      margin-top: 0.625em;
    }
    &__item {
      padding: 1.5em;
      &-text {
        margin-top: 1.5em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .delivery {
    &__logo {
      display: none;
    }
    &__items {
      grid-template-columns: 100%;
    }
  }
}
