@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/_filter-slider.scss";
.ui{
  padding: 4em 1em;
  max-width: 50em;
  @include grid100gap(3.25em);
  &__group{
    @include grid100gap(1.25em);
  }
  .m-check,
  .m-radio{
    margin-right: 1em;
  }
}

._flex-center,
._flex-between,
._flex{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.promo-code{
  label{
    margin: 0;
  }
  input{
    border: none;
    border-bottom: 1px solid $black;
    padding-left: 0;
    padding-right: 3em;
  }
  &__field{
    position: relative;
  }
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 2.1875em;
    height: 2.1875em;
    border-radius: 50%;
    @include center;
    img{
      width: .875em;
    }
  }
  &__cancel{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 3em;
    border-bottom: 1px dashed $black;
    opacity: .4;
    font-weight: 500;
    &:hover{
      opacity: 1;
    }
    span{
      font-size: .875em;
    }
  }
  .fg__error-text{
    margin-top: .375em;
  }
  &._success{
    .promo-code__btn{
      background-image: url(../img/check.svg);
      @include bgImgSize(1em);
      background-color: #52B461;
      pointer-events: none;
      img{
        display: none;
      }
    }
  }
  &._error{
    .promo-code__btn{
      background-color: rgba($black,.3);
      pointer-events: none;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.375em;
    @include lastM(0 1.625em 0 0);
    @include center;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
    }
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.43);
    pointer-events: none;
    border-radius: inherit;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.1);
    }
  }
  img{
    transition: all .6s;
  }
}

.compare-btn,
.favourite-btn{
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
}

.favourite-btn{
  width: 1.5em;
  &:hover{
    path{
      stroke: $primary;
    }
  }
  &._added{
    path{
      fill: $secondary;
      stroke: $secondary;
    }
  }
}

.compare-btn{
  width: 1.5em;
  &:hover{
    g{
      opacity: 1;
    }
  }
  &._added{
    g{
      opacity: 1;
    }
    line{
      stroke: $primary;
    }
  }
}

.animated-title{
  opacity: 0;
  transform: translateY(20px);
  transition: all 1.5s;
  &.animated--active{
    opacity: 1;
    transform: translateY(0%);
  }
}

.scroll-up {
  z-index: 22;
  position: fixed;
  right: 50px;
  bottom: 40px;
  transition: all .3s;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  text-align: center;
  background-color: transparent;
  color: rgba($black,.5);
  &:hover {
    color: $black;
  }
  &__round{
    background-color: #EDEDED;
    width: 2.375em;
    height: 2.375em;
    border-radius: 50%;
    position: relative;
    margin-bottom: .25em;
    svg{
      position: absolute;
      width: .75em;
      top: -.5em;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__text{
    font-size: .6875em;
    line-height: 181%;
    text-transform: uppercase;
  }
  &--active {
    visibility: visible;
    opacity: 1;
  }
  &._white{
    color: rgba($white,.5);
    .scroll-up__round{
      background-color: transparent;
    }
    &:hover {
      color: $white;
    }
    path{
      fill: $white;
    }
  }
}

@media screen and (max-width: 992px) {
  .scroll-up{
    display: none;
  }
}
