.about-top{
  padding: 3.125em 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__desc{
    padding-top: 2.875em;
    max-width: 31em;
  }
  .m-arrows{
    margin-top: 3.25em;
    align-self: flex-start;
  }
}

.m-arrows{
  width: 21em;
  display: grid;
  grid-template-columns: .5em .5em .5em .5em .5em .5em .5em .5em;
  justify-content: space-between;
  gap: 2em 0;
  span{
    position: relative;
    width: .5em;
    height: .375em;
    @include bgImgSize(100%);
    background-image: url(../img/arrow-top.svg);
    &:nth-child(16n-7),
    &:nth-child(16n-6),
    &:nth-child(16n-5),
    &:nth-child(16n-4),
    &:nth-child(16n-3),
    &:nth-child(16n-2),
    &:nth-child(16n-1),
    &:nth-child(16n){
      background-image: url(../img/arrow-bottom.svg);
    }
    &:after{
      content: '';
      @include centerAbsolute;
      width: 250%;
      height: 250%;
    }
  }
  svg{
    width: 100%;
    overflow: unset;
  }
  span{
    &:hover{
      animation: rotate-scale-down 0.65s linear both;
    }
  }
}

@keyframes rotate-scale-down {
  0% {
    transform: scale(1) rotateZ(0);
  }
  50% {
    transform: scale(0.5) rotateZ(180deg);
  }
  100% {
    transform: scale(1) rotateZ(360deg);
  }
}

.about-group{
  width: 93.5em;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 3.125em 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__img{
    width: 39em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    width: 48.25em;
  }
  &__text{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 1.5625em;
  }
  @media screen and (min-width: 577px){
    &:nth-child(even){
      .about-group__img{
        order: -1;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about-top{
    padding: 1.875em 0;
    &__inner{
      padding-right: 13.875em;
      @include grid100gap(.5625em);
      overflow: hidden;
      position: relative;
    }
    .m-title{
      display: inline-flex;
      margin-right: auto;
    }
    &__desc{
      padding: 0;
    }
    .m-arrows{
      margin: 0 0 0 -9.5em;
      position: absolute;
      left: 100%;
      bottom: 0;
    }
  }

  .m-arrows{
    gap: 1.5em 0;
    width: 15.875em;
    span{
      width: .375em;
    }
  }

  .about-group{
    padding: 2em 0;
    &__content{
      width: 23.0625em;
    }
    &__img{
      width: 20.625em;
    }
    &__nav,
    &__text{
      margin-top: .9375em;
    }
    ._hide-tablet{
      display: none;
    }
    @media screen and (min-width: 577px){
      .h2{
        br{
          display: none;
        }
      }
      &--type2{
        position: relative;
        @include grid100gap(.9375em);
        .about-group__content{
          width: 100%;
          order: -1;
        }
        .about-group__img{
          order: 2;
          width: calc(50% - .625em);
        }
        .about-group__text{
          display: grid;
          grid-template-columns: repeat(2,calc(50% - .625em));
          gap: 1.25em;
          p{
            margin: 0 !important;
            &:last-child{
              position: absolute;
              width: calc(50% - 1.43em);
              right: 0;
              bottom: 8em;
            }
          }
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about-top{
    padding: 1.5625em 0 3.125em;
    &__inner{
      padding-right: 0;
    }
    .m-arrows{
      margin: 0 0 0 -4.875em;
      top: 0;
      bottom: unset;
    }
  }

  .m-arrows{
    row-gap: 1.25em;
  }

  .about-group{
    position: relative;
    padding: 1.5625em 0;
    @include grid100gap(.9375em);
    &:first-child{
      padding-top: 0;
    }
    &__img{
      order: -1;
    }
    &__img,
    &__content{
      width: 100%;
    }
    &:not(:first-child){
      padding-top: 6.75em;
      .h2{
        position: absolute;
        left: 0;
        top: .86em;
      }
      .about-group__text{
        margin: 0;
      }
    }
  }
}
