.auth{
  padding-left: 50.125em;
  min-height: $vh;
  margin-top: -5.25em;
  background: #f8f8f8;
  &__hint{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2.5em;
    color: #9a9a9a;
    width: 100%;
    padding: 0 1em;
    text-align: center;
  }
}

.auth-menu{
  position: fixed;
  height: $vh;
  top: 0;
  left: 0;
  width: 50.125em;
  background-color: $black;
  background-image: url(../img/auth-bg.svg);
  background-repeat: no-repeat;
  background-position: left top 13.5em;
  background-size: 8em auto;
  border-radius: 0 3.125em 3.125em 0;
  @include center;
  flex-direction: column;
  z-index: 20;
  .burger{
    position: absolute;
    top: 0;
    left: 0;
    background: #2c2c2c;
  }
  .header__logo{
    position: static;
    transform: unset;
    width: 22.625em;
    height: auto;
    svg{
      position: static;
      transform: unset;
      width: 100%;
      height: auto;
      opacity: 1;
    }
    path{
      fill: $white;
    }
    ._view2{
      display: none;
    }
  }
  &__arrows{
    margin-top: 6.25em;
    .m-arrows{
      width: 27em;
      gap: 2.5em 0;
      span{
        width: .625em;
        height: .5em;
        background-image: url(../img/arrow-top2.svg);
        &:nth-child(16n-7),
        &:nth-child(16n-6),
        &:nth-child(16n-5),
        &:nth-child(16n-4),
        &:nth-child(16n-3),
        &:nth-child(16n-2),
        &:nth-child(16n-1),
        &:nth-child(16n){
          background-image: url(../img/arrow-bottom2.svg);
        }
      }
    }
  }
  &__back{
    position: absolute;
    top: 4.375em;
    right: 4.375em;
    display: flex;
    align-items: center;
    color: rgba($white,.7);
    font-weight: 500;
    text-transform: uppercase;
    span{
      font-size: .8125em;
    }
    ._view2{
      display: none;
    }
    svg{
      width: 2em;
      margin-right: .9375em;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.auth-content{
  min-height: $vh;
  background-image: url(../img/auth-bg2.svg);
  background-size: 90% auto;
  background-repeat: no-repeat;
  background-position: right center;
  padding: 6.75em .9375em;
  @include center;
  position: relative;
}

.auth-wrap{
  width: 27.625em;
  max-width: 100%;
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,50%);
  }
  &__link{
    height: 3.75em;
    @include center;
    padding: 0 1.5625em;
    cursor: pointer;
    color: rgba($black,.6);
    transition: all .3s;
    font-weight: 500;
    &._active{
      pointer-events: none;
      border-color: #E4E4E4;
      background-color: $white;
      color: $black;
      span{
        border-color: transparent;
      }
    }
    &:hover{
      cursor: pointer;
      color: $black;
    }
    span{
      font-size: 1em;
      line-height: 125%;
      border-bottom: 1px dashed #828282;
    }
  }
}

.auth-form{
  padding: 2.5em;
  background-color: $white;
  @include grid100gap(1.5em);
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  .m-check{
    margin: -.75em auto 0;
    display: inline-flex;
    span{
      font-size: 1em;
    }
    a{
      white-space: nowrap;
    }
    label:before{
      top: 0;
      transform: unset;
    }
  }
  &__text{
    text-align: center;
    @include grid100gap(.625em);
  }
  &__desc{
    font-size: 1em;
    line-height: 144%;
    opacity: 0.7;
  }
}

.auth-wrap2{
  width: 43.625em;
}

.auth-form2{
  padding-top: 6.25em;
  padding-bottom: 6.25em;
}


@media screen and (min-width: 1920px){
  .auth{
    padding-left: 42%;
  }

  .auth-menu{
    width: 42%;
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 0;
    margin-top: -3.375em;
    &__hint{
      position: static;
      margin-top: 2.5em;
      transform: unset;
      padding: 0;
    }
  }

  .auth-menu{
    width: 100%;
    position: relative;
    background-image: unset;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 0;
    height: unset;
    .burger{
      position: relative;
    }
    .header__logo{
      @include centerAbsolute;
      width: 4em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__arrows{
      display: none;
    }
    &__back{
      position: static;
      margin-right: .9375em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      svg{
        width: 1.0625em;
      }
    }
  }

  .auth-content{
    padding-top: 3.75em;
    padding-bottom: 3.75em;
    min-height: unset;
    display: block;
  }

  .auth-wrap{
    width: 24.125em;
    margin: 0 auto;
  }

  .auth-form{
    padding: 1.25em;
    .m-check{
      span{
        font-size: .9375em;
      }
    }
  }

  .auth-wrap2{
    width: 41.125em;
  }
}


@media screen and (max-width: 576px){
  .auth-wrap{
    width: 100%;
  }

  .auth-form{
    &__fields{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    .m-check{
      label{
        &:before{
          top: 0;
          transform: unset;
        }
      }
    }
  }
}
