.breadcrumbs {
    padding: 2em 0 1.5625em;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    li{
        position: relative;
        display: inline;
        &:not(:first-child) {
            margin-left: 1em;
            a{
                &:before {
                    content: '';
                    position: absolute;
                    z-index: 1;
                    top: .5em;
                    right: 100%;
                    margin-right: .75em;
                    width: .0625em;
                    height: .75em;
                    background-color: $black;
                    background-position: center top;
                    display: block;
                }
            }
        }
        &:last-child{
            pointer-events: none;
            color: rgba($black,.4);
        }
    }
    a{
        font-size: .75em;
    }
}

.page-top{
    &__title{
        text-align: center;
        .h2{
            display: inline;
        }
    }
    &__count{
        transform: translateY(-240%);
        display: inline-block;
        font-size: .8125em;
        line-height: 115%;
        color: #C5C5C5;
        margin-left: .25em;
    }
    &._type2{
        background-size: cover;
        background-position: center;
        position: relative;
        padding: 6em 0;
        color: $white;
        .breadcrumbs{
            padding-top: 0;
            li{
                &:not(:first-child) {
                    a{
                        &:before {
                            background-color: $white;
                        }
                    }
                }
                &:last-child{
                    color: rgba($white,.8);
                }
            }
        }
        &:after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.4);
            pointer-events: none;
            border-radius: inherit;
        }
        .container{
            position: relative;
            z-index: 2;
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1em 0 1.25em;
        li{
            font-size: .6875em;
            line-height: 109%;
        }
    }

    .page-top{
        &__title{
            margin-top: -.625em;
        }
        &__count{
            transform: translateY(-80%);
        }
        &._type2{
            padding: 2.125em 0;
        }
    }

}


