.delivery-address{
  padding: 1.1875em;
  margin-top: 1.875em;
  border: .375em solid #F8F8F8;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title{
    color: rgba($black,.8);
    font-weight: 500;
    img{
      width: 2.75em;
      margin-right: .625em;
    }
  }
}

.empty-address{
  max-width: 52.5em;
  .empty-search{
    margin-bottom: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .delivery-address{
    @include grid100gap(.9375em);
  }

  .empty-address{
    display: flex;
    flex-direction: column;
    align-items: center;
    .empty-search{
      margin-bottom: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .empty-address{
    .empty-search{
      margin-bottom: 1.5em;
    }
  }
}
