.instagram{
  padding: 5em 0;
  background-color: #f4f4f4;
  background-image: url(../img/instagram-bg.svg);
  background-repeat: no-repeat;
  background-size: 3.875em auto;
  background-position: right bottom 11.75em;
  &__top{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__social{
    display: flex;
    align-items: center;
    color: rgba(#777,.5);
    font-weight: 500;
    margin-bottom: .625em;
    &:hover{
      color: #777;
      img{
        opacity: 1;
      }
    }
    img{
      width: 1.25em;
      opacity: .22;
      transition: all .3s;
      margin-right: .5625em;
    }
  }
  &__text{
    padding: .9375em 0 0 3.125em;
    font-weight: 500;
    text-transform: uppercase;
    color: #8c8c8c;
    max-width: 22.875em;
    .text13{
      line-height: 169%;
    }
  }
}

.m-title{
  position: relative;
  svg{
    position: absolute;
    width: 13.375em;
    top: -.1875em;
    right: -1.75em;
  }
  .h2{
    position: relative;
    z-index: 2;
  }
  .animated--active{
    path{
      animation: dash 2s ease forwards;
    }
  }
  &--arrow{
    svg{
      width: 5.125em;
      top: 2.875em;
      left: 100%;
      margin-left: 3em;
    }
    ._view2{
      display: none;
    }
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

.swiper-nav{
  &._united{
    display: flex;
    .swiper-button{
      position: relative;
      top: unset;
      left: unset;
      right: unset;
      margin: 0;
      z-index: 1;
      &-prev{
        margin-right: -1px;
      }
      &:hover{
        z-index: 2;
      }
    }
  }
}

.swiper-button{
  border: 1px solid #d8d8d8;
  width: 3.125em;
  height: 3.125em;
  transition: all .3s;
  svg{
    width: .875em;
  }
  &:hover{
    border-color: $black;
  }
}

.instagram-slider{
  margin-top: 3em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + .9375em);
    }
    &-slide{
      width: 20%;
      padding-right: .9375em;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 3em;
    }
  }
}

.instagram-item{
  @include imgCover(100%);
}


@media screen and (max-width: 992px){
  .instagram{
    padding: 1.875em 0;
    background-size: 1.25em auto;
    background-position: right bottom 3.75em;
    &__social{
      span{
        font-size: .875em;
      }
      img{
        width: 1.0625em;
      }
    }
    &__text{
      padding: .625em 0 0;
      max-width: 21em;
    }
  }

  .m-title{
    svg{
      width: 6.6875em;
      top: -.3125em;
      right: -1em;
    }
    &--arrow{
      svg{
        width: 2.8125em;
        top: 1em;
        margin-left: .5em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .instagram-slider{
    margin-top: 1.5em;
    .swiper{
      &-nav{
        margin-bottom: 1.5em;
      }
      &-slide{
        width: 25%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .instagram{
    overflow: hidden;
    background-image: url(../img/instagram-bg2.svg);
    background-size: 3.625em auto;
    background-position: right bottom 1em;
  }

  .instagram-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-nav{
        display: none;
      }
      &-slide{
        width: 9.875em;
      }
    }
  }
}
