.payment {
  .about-top {
    padding-bottom: 1.875em;
  }
  .m-arrows{
    align-self: flex-end;
  }
  .m-title--arrow svg {
    top: unset;
    bottom: 0;
  }
  .about-top__desc {
    max-width: 27.8125em;
    display: flex;
    align-items: flex-end;
    padding: 1em;
  }
  &-main__group {
    &:not(:first-child) {
      margin-top: 3.125em;
    }
  }
  &__subtitle {
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 500;
    color: #090909;
  }
  &__cards {
    max-width: 58.75em;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
    margin-top: 1.25em;
  }
  &__card {
    min-height: 15.5em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 0.1875em solid #EEEEEE;
    padding: 2em;
    &-logo {
      max-width: 8.125em;
      img {
        max-width: 100%;
      }
    }
    &-title {
      margin-top: 2.5em;
      p {
        font-size: 1.125em;
        line-height: 130%;
        font-weight: 600;
        text-transform: uppercase;
      }
    }
    &-text {
      text-transform: uppercase;
      margin-top: 2.5em;
      p {
        font-size: 0.75em;
        line-height: 130%;
        color: #8C8C8C;
      }
    }
  }
  &-info {
    display: flex;
    justify-content: space-between;
    gap: 2.8125em;
    margin-top: 3.125em;
    .text {
      p:not(:last-child) {
        margin: 0 0 0.5em;
      }
      ol {
        gap: 0;
        margin-bottom: 0.5em;
      }
    }
  }
  &-info__img {
    width: 34.75em;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &-info__text {
    width: calc(100% - 37.5625em);
  }
  &-info__mob-text {
    display: none;
  }
  &-desc {
    margin-top: 4em;
    .text {
      border-bottom: 0.0625em solid #EAEAEA;
      padding: 4em 0;
      &:nth-child(1) {
        border-top: 0.0625em solid #EAEAEA;
      }
    }
    .text ol{
      gap: .75em;
    }
    .text li ul {
      gap: 0;
      li{
        font-size: 1em;
      }
    }
    .text li ul li {
      padding-left: 1.5em;
    }
    .text li ul li:before {
      top: 0.75em;
      left: 0.75em;
      width: 0.1875em;
      height: 0.1875em;
      background: #000000;
      border-radius: 50%;
    }
    .text p span {
      font-weight: 500;
    }
    .text p a {
      color: #ff6001;
      border-bottom: 0.0625em solid #ff6001;
      &:hover {
        border-bottom: unset;
      }
    }
  }
}
.payment-subscribe {
  padding-top: 3.75em;
}

@media screen and (max-width: 992px) {
  .payment {
    .about-top__desc {
      max-width: 30.3125em;
      padding: 0;
    }
    &-main__group {
      &:not(:first-child) {
        margin-top: 1.875em;
      }
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 2em / 3));
      gap: 1em;
    }
    &__card {
      min-height: 11.125em;
      padding: 1.25em;
      &-title {
        margin-top: 1em;
        p {
          font-size: 0.875em;
        }
      }
      &-text {
        margin-top: 1em;
      }
    }
    &-info {
      gap: 1.5625em;
      margin-top: 1.875em;
    }
    &-info__img {
      width: 20em;
    }
    &-info__text {
      width: calc(100% - 21.5625em);
    }
    &-info__mob-text {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1.5625em / 2));
      gap: 1.5625em;
      margin-top: 1.5625em;
    }
    .removal-mob {
      display: none;
    }
    &-desc {
      margin-top: 1.875em;
      .text {
        padding: 1.875em 0;
      }
    }
  }
  .payment-subscribe {
    padding-top: 1.875em;
  }
}

@media screen and (max-width: 576px) {
  .payment {
    .about-top {
      padding-bottom: 1.5625em;
    }
    &-main__group {
      &:nth-child(1) {
        .payment__card-title {
          br {
            display: none;
          }
        }
      }
    }
    &__cards {
      grid-template-columns: 100%;
    }
    &__card {
      min-height: 8.75em;
    }
    &-info {
      flex-direction: column;
    }
    &-info__img {
      width: 100%;
    }
    &-info__text {
      width: 100%;
    }
    &-info__mob-text {
      display: none;
    }
    .removal-mob {
      display: block;
    }
  }
}
