.politics {
  position: relative;
  padding-bottom: 7.1875em;
  .container {
    position: relative;
    z-index: 2;
  }
  .about-top {
    padding-bottom: 1.875em;
  }
  .m-arrows{
    align-self: flex-end;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 2.5em;
  }
  &__left {
    max-width: calc(100% - 21.25em);
  }
  &__right {
    position: relative;
    width: 18.75em;
    flex-shrink: 0;
    padding-top: 3em;
  }
  &__mail {
    background-color: $white;
    z-index: 2;
    position: sticky;
    top: 5em;
    left: 0;
    display: inline-flex;
    flex-direction: column;
    gap: 1.5em;
    border: 0.1875em solid #EEEEEE;
    padding: 2em;
    a span {
      font-size: 1.4375em;
      line-height: 130%;
      font-weight: 500;
      color: #454545;
      border-bottom: 0.0625em solid #454545;
      &:hover {
        color: #ff6001;
        border-color: #ff6001;
      }
    }
    p {
      font-size: 0.75em;
      line-height: 130%;
      font-weight: 500;
      color: #8C8C8C;
      text-transform: uppercase;
    }
  }
  .text {
    ul {
      gap: 0.5em;
    }
    h4 {
      font-weight: 700;
    }
    h5 {
      font-size: 1.125em;
      line-height: 125%;
      font-weight: 500;
      margin-bottom: 1em;
    }
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 26.75em;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .politics {
    padding-bottom: 1.875em;
    &__wrap {
      flex-direction: column;
      gap: 1.875em;
    }
    &__left {
      max-width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
      padding-top: 0;
    }
    &__mail {
      position: static;
      width: 100%;
      padding: 1.5em;
    }
    .text {
      h5 {
        font-size: 0.9375em;
      }
    }
    &__img {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .politics {
    h1 {
      font-size: 1.375em;
    }
    .m-arrows {
      display: none;
    }
  }
}
