.header{
  position: fixed;
  width: 100%;
  top: 0;
  transition: all .3s;
  z-index: 20;
  &._fix{
    background-color: $white;
    box-shadow: 0 .6875em 1.5625em rgba(0, 0, 0, 0.05), 0 2.8125em 2.8125em rgba(0, 0, 0, 0.04), 0 6.25em 3.75em rgba(0, 0, 0, 0.03), 0 11.25em 4.5em rgba(0, 0, 0, 0.01), 0 17.5em 4.875em rgba(0, 0, 0, 0);
    .running-line{
      margin-top: -4.25em;
    }
    .header__inner{
      min-height: 4em;
    }
    .header__logo{
      width: 6.0625em;
      height: 4em;
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .burger{
      width: 4em;
      height: 4em;
    }
  }
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #eee;
  }
  .container{
    //max-width: 100%;
    padding: 0 5.5em;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 5.25em;
    transition: all .3s;
    @media screen and (max-width: 1920px){
      padding-left: 9em;
    }
  }
  &__logo{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    width: 6.25em;
    height: 5.25em;
    @include center;
    flex-shrink: 0;
    svg,
    img{
      transition: all .0s;
      width: 100%;
      display: block;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
    }
  }
  .running-line{
    transition: all .3s;
    background-color: $black;
    border-color: $black;
    color: $white;
    &__item{
      margin-right: 1.875em;
      &:after{
        background-image: url(../img/arrow-right5.svg);
        margin-left: 1.875em;
      }
      &-desc{
        font-size: 1em;
        margin-top: -.125em;
        margin-left: .625em;
      }
    }
  }
  &._white{

  }
  &._open{

  }
}

._white-header{
  .header:not(._fix){
    color: $white;
    .header__logo{
      path{
        fill: $white;
      }
    }
    .header-search__field{
      path{
        stroke: $white;
      }
      input{
        border-color: $white;
      }
    }
    .header-cart{
      color: #D2D2D2;
    }
    .header-action__user,
    .header-cart,
    .header-favourite{
      path{
        stroke: $white;
      }
    }
    .header-search__field,
    .header-cart{
      background-color: transparent;
    }
  }
  .search-modal,
  .header-menu{
    color: $black;
  }
}

._open-menu{
  .header-menu{
    opacity: 1;
    pointer-events: unset;
    transition-delay: 0s;
    &__content{
      transform: translateX(0);
    }
  }
}

.nav{
  .menu{
    display: flex;
    align-items: center;
    font-weight: 500;
    li{
      margin-right: 3.5em;
    }
    a{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.header-action{
  &__user{
    margin-left: 2.5em;
    @include center;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    svg,
    img{
      width: 1.5em;
    }
  }
}

.header-cart{
  color: #4B4B4B;
  margin-left: 2.5em;
  &:hover{
    color: #4B4B4B;
    path{
      stroke: $primary;
    }
  }
  svg,
  img{
    width: 1.5em;
    margin-right: .25em;
  }
  &__count{
    margin-left: .25em;
  }
}

.m-count{
  padding: 0 .3125em;
  height: 1em;
  min-width: 1em;
  @include center;
  border-radius: .375em;
  background-color: $primary;
  color: $white;
  font-weight: 500;
  span{
    font-size: .5625em;
  }
}

.header-favourite{
  margin-left: 2.5em;
  width: 1.5em;
  position: relative;
  &:hover{
    path{
      stroke: $primary;
    }
  }
  svg,
  img{
    width: 100%;
    display: block;
  }
  &__count {
    position: absolute;
    left: 100%;
    bottom: 100%;
    margin-bottom: -.25em;
    color: #BABABA;
    font-size: .6875em;
  }
}

.header-search{
  &__field{
    width: 10.75em;
    height: 1.875em;
    position: relative;
    svg,
    img{
      position: absolute;
      width: 1em;
      left: 0;
      bottom: 0;
      cursor: pointer;
    }
    input{
      background-color: transparent;
      width: 100%;
      height: 100%;
      border-radius: 0;
      border: none;
      border-bottom: 1px solid $black;
      padding-left: 2em;
      font-size: .8125em;
      color: $black;
      &::placeholder{
        color: #ADADAD;
      }
    }
  }
}

.burger{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 5.25em;
  height: 5.25em;
  @include center;
  img{
    width: 1.75em;
  }
  &:before{
    content: '';
    position: absolute;
    width: .375em;
    height: .375em;
    border-radius: 50%;
    background-color: $primary;
    right: -.125em;
    bottom: -.125em;
    box-shadow: 0 0 .5em rgba(255, 96, 1, 0.78);
    animation: 2s infinite pulse;
  }
}

.mob-panel{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &._fix{
      .running-line{
        margin-top: -2em;
      }
      .header__inner{
        min-height: 2.5em;
      }
      .header__logo{
        width: 3.125em;
        height: 2.5em;
      }
      .burger{
        width: 2.5em;
        height: 2.5em;
        &:before{
          width: .25em;
          height: .25em;
        }
      }
      .header-cart{
        width: 2.5em;
        height: 2.5em;
      }
      .header-search{
        bottom: .5em;
      }
    }
    .container{
      padding: 0 5.25em;
    }
    &__inner{
      min-height: 3.375em;
      padding-left: 0;
    }
    &__logo{
      width: 4em;
      height: 3.375em;
    }
    .running-line{
      height: 2em;
      display: flex;
      align-items: center;
      &__item{
        margin-right: .9375em;
        &:after{
          margin-left: .9375em;
        }
        &-desc{
          font-size: .75em;
          margin-top: -.25em;
        }
      }
    }
  }

  .nav{
    opacity: 0;
    pointer-events: none;
    width: 0;
    height: 0;
    overflow: hidden;
  }

  .header-cart,
  .burger{
    width: 3.375em;
    height: 3.375em;
  }

  .header-cart{
    position: absolute;
    bottom: 0;
    right: 0;
    background: #eaeaea;
    margin: 0;
    justify-content: center;
    &__text{
      display: none;
    }
    img{
      width: 1.75em;
    }
    &__count{
      position: absolute;
      top: .6875em;
      right: .5em;
    }
  }

  .header-favourite{
    margin-left: 2.1875em;
  }

  .header-search{
    position: absolute;
    bottom: 1em;
    left: 5.25em;
    &__field{
      width: 1.5em;
      height: 1.5em;
      input{
        display: none;
      }
      svg,
      img{
        @include centerAbsolute;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &._fix{
      .header__inner{
        min-height: 1.875em;
      }
      .header__logo{
        width: 2.375em;
        height: 1.875em;
      }
      .header-search{
        bottom: 0;
        &__field{
          width: 1.875em;
          height: 1.875em;
          img{
            width: .75em;
          }
        }
      }
      .burger{
        width: 1.875em;
        height: 1.875em;
        img{
          width: 1.125em;
        }
      }
    }
  }

  .header-action__user,
  .header-favourite,
  .header-cart{
    display: none;
  }

  .header-search{
    bottom: 0;
    right: 0;
    left: unset;
    &__field{
      width: 3.375em;
      height: 3.375em;
      background: #eaeaea;
    }
  }

  .mob-panel{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 2.75em;
    padding: 0 2.1875em;
    background-color: $white;
    box-shadow: 0 0 .875em rgba(0, 0, 0, 0.15);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    &__btn{
      width: 1.5em;
      @include center;
      position: relative;
      svg,
      img{
        width: 100%;
        display: block;
      }
      .m-count{
        position: absolute;
        top: -.1875em;
        right: -.5em;
      }
    }
  }
}
