.article{
  padding: 3.125em 0;
  &__inner{
    width: 82.5em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__date{
    display: flex;
    align-items: center;
    margin-top: .5em;
    color: rgba($black,.7);
    img{
      width: 1.25em;
      margin-right: .3125em;
    }
  }
}

.article-text{
  margin-top: 2.5em;
  img{
    width: 100%;
    display: block;
  }
}

.article-gallery{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: 1.25em;
  margin-bottom: 1em;
  &:last-child{
    margin-bottom: 0;
  }
  img{
    width: 100%;
    min-height: 100%;
    display: block;
  }
}

.blog-collection{
  padding: 2.5em 0 7.5em;
  overflow: hidden;
}

.blog-slider,
.catalog-slider{
  margin-top: 2.5em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-nav{
      position: absolute;
      bottom: 100%;
      right: 0;
      margin-bottom: 2.5em;
    }
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      padding-right: 1.25em;
    }
  }
}

.blog-slider{
  .swiper{
    &-slide{
      width: 50%;
    }
  }
  .blog-card{
    margin: 0;
    &__img{
      @include imgCover(52.2%);
    }
    @media screen and (min-width: 993px){
      &__content{
        margin-top: 1.5em;
        display: grid;
        grid-template-columns: 13.75em calc(100% - 14.75em);
        gap: 1em;
      }
      &__bottom{
        margin: 0;
      }
      &__title{
        font-size: 1.25em;
        line-height: 125%;
        font-weight: 500;
      }
    }
    &__subtitle{
      font-size: .875em;
      color: $black;
      font-weight: 600;
    }
  }
}

.team-member2{
  padding: 0;
}


@media screen and (max-width: 992px){
  .article{
    padding: 1.875em 0;
    &__date{
      margin-top: .625em;
    }
  }

  .article-text{
    margin-top: .625em;
  }

  .blog-collection{
    padding: 1.875em 0;
  }

  .blog-slider,
  .catalog-slider{
    margin-top: .9375em;
    .swiper-nav{
      margin-bottom: .9375em;
    }
  }

  .blog-slider{
    .blog-card{
      &__img{
        padding-bottom: 67.8%;
      }
    }
  }

  .team-member2{
    padding-top: 1.875em;
  }
}


@media screen and (max-width: 576px){
  .article{
    padding: 1.5625em 0;
  }

  .article-gallery{
    grid-template-columns: 100%;
  }

  .blog-slider{
    margin-top: .9375em;
    .swiper-slide{
      width: 100%;
    }
    .blog-card{
      &__img{
        padding-bottom: 85.7%;
      }
    }
  }

  .team-member2{
    padding-bottom: 2.625em;
  }
}
