.radio-field{
  border: 1px solid #f4f4f4;
  padding: 1.25em 1.25em 1.25em 3.375em;
  &__icon{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    border: 1px solid #6a6a6a;
    width: 1.5em;
    height: 1.5em;
    @include bgImgSize(0);
    transition: all .0s;
    border-radius: 50%;
    background-image: url(../img/radio.svg);
  }
  input:checked~.radio-field__icon{
    border: none;
    background-size: 100% auto;
  }
  &__title{
    font-weight: 600;
    font-size: 1.0625em;
    line-height: 153%;
  }
  &__desc{
    font-size: 1em;
    line-height: 144%;
    color: #9b9b9b;
    margin-top: .375em;
  }
}

.order-delivery{
  &>input{
    width: 1.5em;
    height: 1.5em;
    left: 1.25em;
    top: 1.25em;
  }
  &__tabs{
    padding: .1875em;
    display: inline-flex;
    background: #f4f4f4;
    margin-top: .6875em;
  }
  &__tab{
    position: relative;
    label{
      padding: 0 1.0625em;
      height: 2em;
      @include center;
      width: 100%;
      font-weight: 500;
    }
    span{
      font-size: .875em;
    }
    input:checked~label{
      background-color: $white;
    }
  }
  &__content{
    margin-top: 1.5625em;
    display: none;
    &._active{
      display: block;
    }
  }
  &__select{
    height: 2.25em;
    width: 19.5em;
    margin-top: -.625em;
    span{
      font-size: .75em;
    }
  }
}

.order-pay{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 3.125em / 3));
  gap: .9375em 1.5625em;
}

.pay-type{
  &__images{
    display: flex;
    align-items: center;
    margin-top: .625em;
    img{
      margin-right: 1.25em;
      max-width: 2.8125em;
      max-height: 1.125em;
    }
  }
  &__parts{
    margin-top: .625em;
    width: 5.625em;
  }
}

.order-auth{
  margin-top: -1.25em;
  border: .5625em solid #F9F9F9;
  padding: 1.25em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 15.5em) 14em;
    gap: 1.5em;
    margin-top: 1.5em;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  &__btn{
    align-self: flex-end;
  }
  &__link{
    grid-column: 1/-1;
    margin-top: -.75em;
  }
}

.orange-link{
  font-size: .9375em;
  line-height: 120%;
  text-decoration: underline;
  color: $primary;
}

.order-other{
  margin: 2.5em 0 -1em;
  .m-check{
    span{
      font-size: 1em;
    }
  }
  &__bottom{
    margin-top: 1.5em;
    display: none;
  }
}


@media screen and (max-width: 992px){
  .order-delivery{
    padding: 1.25em;
    .radio-field__title{
      padding-left: 2em;
    }
  }

  .order-pay{
    grid-template-columns: 100%;
  }

  .order-auth{
    padding: .6875em;
    &__title{
      text-align: center;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 11.5em;
      margin: 0 auto;
    }
    &__link{
      text-align: center;
    }
  }
}


@media screen and (max-width: 576px){
  .order-delivery{
    &__tab{
      label{
        padding: 0 .625em;
      }
    }
    &__select{
      width: 100%;
    }
  }

  .order-auth{
    &__fields{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
