.products{
  padding: 5em 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .m-title--arrow {
    svg{
      margin-left: 4em;
      top: -.875em;
    }
  }
  &__year{
    position: absolute;
    top: -.9375em;
    left: 100%;
    margin-left: .9375em;
    font-family: $font2;
    span{
      font-size: 1em;
      line-height: 112%;
    }
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.product-card{
  display: block;
  &:hover{
    color: $black;
    .favourite-btn,
    .product-card__img:after,
    .product-card__content{
      opacity: 1;
    }
  }
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(101.5%);
    &:after{
      transition: all .3s;
      opacity: 0;
    }
  }
  &__badges{
    position: absolute;
    padding: .625em;
    display: flex;
    left: 0;
    bottom: 0;
    .m-badge{
      margin-right: .3125em;
    }
  }
  &__bottom{
    min-height: 3.5em;
    padding: .625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__desc,
  &__title{
    font-weight: 500;
    font-size: .9375em;
    line-height: 167%;
    text-transform: uppercase;
  }
  &__btn{
    width: 1.125em;
    @include center;
    img{
      width: 100%;
    }
  }
  &__content{
    width: 100%;
    @include centerAbsolute;
    @include grid100gap(.625em);
    text-align: center;
    padding: 5em;
    transition: all .3s;
    color: $white;
    opacity: 0;
    pointer-events: none;
  }
  &__price{
    font-size: 1.375em;
    line-height: 95%;
    text-transform: uppercase;
  }
  .favourite-btn{
    position: absolute;
    top: .625em;
    right: .625em;
    @include center;
    z-index: 2;
    opacity: 0;
  }
}

.m-badge{
  padding: 0 .25em;
  height: 1.3125em;
  display: flex;
  align-items: center;
  background-color: $primary;
  color: $white;
  font-weight: 600;
  text-transform: uppercase;
  span{
    font-size: .625em;
  }
  &--blue{
    background-color: $secondary;
  }
}

.m-video{
  width: 100%;
  display: block;
}

.for-who{
  padding: 1.875em 0 3.75em;
  &__top{
    width: 47.5em;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__bottom{
    margin-top: 3.125em;
    position: relative;
    &:first-child{
      margin-top: 0;
    }
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__content{
    @include centerAbsolute;
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    color: $white;
    font-size: 6.5em;
    line-height: 54%;
    text-shadow: 0 .25em 4em rgba(0, 0, 0, 0.15);
  }
  &__link{
    margin-top: 4.375em;
  }
  .m-badge{
    padding: 0 .5em;
    height: 1.5625em;
    margin-bottom: 1.875em;
    font-weight: 500;
    span{
      font-size: .875em;
      letter-spacing: 0.02em;
    }
  }
}

.for-who2{
  @media screen and (min-width: 993px){
    padding-bottom: 0;
  }
}


@media screen and (max-width: 992px){
  .products{
    padding: 3.5em 0;
    .m-title--arrow svg{
      margin-left: 2.5em;
      top: 0;
    }
    &__year{
      top: -1.125em;
      margin-left: .5em;
      span{
        font-size: .8125em;
        line-height: 138%;
      }
    }
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(3,calc(100% / 3 - .625em));
      gap: .9375em;
    }
  }

  .product-card{
    &__bottom{
      min-height: 2.75em;
      padding: .625em 0;
    }
    &__img:after,
    &__content{
      display: none;
    }
    &__title,
    &__desc{
      font-size: .875em;
      line-height: 164%;
    }
    .favourite-btn{
      opacity: 1;
      width: 1em;
      &:not(._added){
        path{
          stroke: $black;
        }
      }
    }
  }

  .for-who{
    padding: .3125em 0 1.125em;
    &__top{
      width: 30em;
    }
    &__bottom{
      margin-top: 1.5625em;
    }
    .m-badge{
      padding: 0 .3125em;
      height: 1.3125em;
      margin-bottom: .25em;
      span{
        font-size: .5625em;
      }
    }
    &__title{
      font-size: 2.0625em;
      line-height: 118%;
    }
    &__link{
      margin-top: 1.3125em;
    }
  }
}


@media screen and (max-width: 576px){
  .products{
    padding: 1.875em 0;
    .m-title--arrow {
      max-width: 12em;
      svg{
        left: unset;
        right: -.25em;
        top: unset;
        bottom: 0;
      }
    }
    &__inner{
      position: relative;
      padding-bottom: 3.375em;
    }
    &__top{
      max-width: 12em;
    }
    &__year{
      top: -.625em;
      left: 7.1875em;
      margin-left: 0;
    }
    &__link{
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      white-space: nowrap;
    }
    &__grid{
      margin-top: .9375em;
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    }
    .product-card:nth-child(3){
      grid-column: 1/-1;
      .favourite-btn{
        width: 1.5em;
        top: .625em;
        right: .625em;
      }
      .product-card__bottom{
        min-height: 3.3125em;
      }
      .product-card__title{
        font-size: .875em;
      }
    }
  }

  .product-card{
    &__title{
      font-size: .75em;
    }
  }

  .for-who{
    padding: 1.875em 0 6em;
    &__bottom{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
    &__content{
      height: 100%;
      justify-content: center;
    }
    &__link{
      position: absolute;
      top: 100%;
      margin-top: .625em;
    }
  }
}
