.lk,
.lk .container{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.lk{
  &__inner{
    flex-grow: 1;
    display: grid;
    grid-template-columns: 17.125em calc(100% - 19.625em);
    gap: 2.5em;
  }
  &__col{
    padding: 3.125em 0;
    border-right: 1px solid #eee;
  }
  &__col2{
    padding-bottom: 8.125em;
  }
  .page-top{
    position: relative;
  }
  &__arrows{
    top: 4em;
    right: 0;
    position: absolute;
  }
  &__arrows2{
    display: none;
  }
  .breadcrumbs{
    text-align: left;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 117%;
    opacity: 0.8;
    margin-top: .6em;
  }
  &__content{
    padding-top: 1.875em;
  }
}

.lk-menu{
  position: sticky;
  top: 5em;
}

.lk-link{
  min-height: 2.75em;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  padding-right: 2.5em;
  &._active,
  &:hover{
    color: $primary;
  }
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .4375em;
    @include center;
    img,
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: .8125em;
    line-height: 150%;
    font-weight: 600;
    padding-right: .5em;
  }
  .m-count{
    margin-left: auto;
  }
}

.user-data{
  max-width: 52.5em;
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  &__btn{
    min-width: 16em;
    margin-top: 1.5em;
  }
  .empty-search{
    margin-top: 1.5em;
  }
  &__bottom{
    margin-top: 1.5em;
    .user-data__btn{
      margin: 0 1.5em 0 0;
    }
  }
}

.user-password{
  max-width: 28em;
  .user-data__fields{
    max-width: 22.625em;
    grid-template-columns: 100%;
    gap: 1.875em;
  }
  .user-data__btn{
    margin-top: 1.875em;
  }
}

.delete-btn--medium{
  font-weight: 500;
  color: $black;
  text-transform: unset;
  span{
    font-size: .9375em;
    border: none;
  }
}

.lk-buttons{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - .625em / 3));
  gap: .3125em;
}

.lk-btn{
  min-height: 9.25em;
  position: relative;
  padding: 1.5em;
  background-color: #FBFBFB;
  @include center;
  flex-direction: column;
  color: rgba($black,.54);
  text-align: center;
  .m-count{
    position: absolute;
    top: 2.375em;
    left: 50%;
    margin-left: 1.125em;
  }
  &__icon{
    height: 4.25em;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-bottom: .625em;
    svg{
      max-width: 100%;
      max-height: 100%;
      width: auto;
      height: auto;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 118%;
    font-weight: 500;
    ._view2{
      display: none;
    }
  }
  &:hover,
  &._active{
    background-color: $black;
    border-color: $black;
    color: $white;
    line,
    path{
      stroke: $white;
    }
  }
}

.lk-mob-menu{
  display: none;
}


@media screen and (max-width: 992px){
  .lk{
    //overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
      position: relative;
    }
    &__col{
      position: absolute;
      top: 8.75em;
      left: 0;
      border: none;
      padding: 0;
    }
    &__col2{
      display: flex;
      flex-direction: column;
      padding-bottom: 1.875em;
    }
    .page-top{
      text-align: center;
      @media screen and (min-width: 577px) {
        width: calc(100% + 3.75em);
        margin-left: -1.75em;
        padding-left: 1.75em;
        padding-right: 1.75em;
        border-bottom: $border;
        padding-bottom: 1.875em;
      }
    }
    .breadcrumbs{
      text-align: center;
      padding-bottom: .625em;
      padding-top: 1.875em;
    }
    &__arrows,
    &__arrows2{
      width: 4.25em;
      overflow: hidden;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      .m-arrows{
        width: 13.875em;
      }
    }
    &__arrows{
      right: 1.75em;
    }
    &__arrows2{
      display: block;
      left: 1.75em;
    }
    &__desc{
      font-size: .9375em;
      margin-top: .67em;
    }
    @media screen and (min-width: 577px){
      &__content{
        flex-grow: 1;
        padding: 1.5em 0 1.5em 1.5em;
        border-left: $border;
        width: calc(100% - 12.5em);
        margin-left: auto;
        min-height: 25em;
      }
      &--main{
        .page-top{
          border: none;
          padding-bottom: 0;
        }
        .lk__col{
          display: none;
        }
        .lk__content{
          padding: .625em 0 0;
          width: 100%;
          border: none;
        }
      }
    }
  }

  .user-data{
    max-width: 100%;
    &__fields{
      grid-template-columns: 100%;
    }
    &__btn{
      min-width: unset;
      width: 100%;
    }
    &--address2{
      .user-data__fields{
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 1.25em;
      }
    }
    &--address{
      .user-data__bottom,
      .user-data__fields{
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 1.25em;
      }
      .user-data__bottom{
        display: grid;
        margin-top: 1.25em;
        .user-data__btn{
          margin: 0;
          width: 100%;
        }
      }
    }
  }

  .user-password{
    .user-data__fields{
      gap: 1.25em;
      max-width: 100%;
    }
    .user-data__btn{
      margin-top: 1.25em;
    }
  }

  .lk-link{
    padding-right: .25em;
  }

  .lk-buttons{
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .625em;
  }

  .lk-btn{
    padding: .9375em;
    min-height: 7.75em;
    &__title{
      font-size: .9375em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px) {
  .lk{
    padding-bottom: 1.875em;
    &__arrows,
    &__arrows2{
      transform: unset;
      top: 1.5em;
      width: 2.25em;
    }
    &__arrows{
      right: 0;
    }
    &__arrows2{
      left: 0;
    }
    &__col{
      display: none;
    }
    &--main{
      .lk__content{
        padding-top: .625em;
      }
    }
  }

  .lk-buttons{
    grid-template-columns: repeat(2,calc(50% - .3125em));
  }

  ._open-lk-menu{
    .header:before{
      display: none;
    }
    .header__inner{
      opacity: 0;
      pointer-events: none;
    }
    .lk-mob-menu{
      opacity: 1;
      pointer-events: unset;
    }
    ._toggle-lk-menu{
      path{
        stroke: $primary;
      }
    }
  }

  .lk-mob-menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    display: block;
    position: fixed;
    height: calc(100vh - 2.75em);
    left: 0;
    bottom: 2.75em;
    background-color: $white;
    width: 100%;
    padding: 1.875em .9375em;
    &__top{
      text-align: center;
      margin-bottom: .625em;
      opacity: .8;
    }
  }

  .user-data{
    &--address2{
      .user-data__fields{
        grid-template-columns: 100%;
      }
    }
    &--address{
      .user-data__bottom,
      .user-data__fields{
        grid-template-columns: 100%;
      }
      .user-data__bottom{
        gap: 1.5em;
      }
      .delete-btn2{
        margin: 0 auto;
      }
    }
  }
}
