.sizes-page{
  .about-top__inner{
    align-items: flex-end;
  }
  .about-top__desc{
    padding-bottom: .75em;
  }
  .m-arrows{
    align-self: unset;
  }
  .m-title{
    max-width: 47.5em;
    svg{
      top: unset;
      bottom: 0;
      margin-left: -24em;
    }
  }
}

.size-info{
  padding: 5em 0;
  background-color: #F5F5F5;
  &__inner{
    display: grid;
    grid-template-columns: 35.875em 28.75em 28.75em;
    align-items: center;
    justify-content: space-between;
  }
  &__img{
    width: 100%;
    display: block;
  }
}

.size-info2{
  padding: 4em 0 8.125em;
  background-image: url(../img/size-bg.svg);
  background-repeat: no-repeat;
  background-position: left 0 top 13.125em;
  background-size: 27.5em auto;
  &__inner{
    display: grid;
    grid-template-columns: 30.25em calc(100% - 33.25em);
    gap: 1.25em 3em;
  }
}

.sizes-table{
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,td{
    text-align: center;
    padding: 1.05em;
    line-height: 159%;
    font-size: 1.0625em;
    width: 25%;
  }
  th{
    font-weight: 500;
    background: #f5f5f5;
    br{
      display: none;
    }
  }
  td{
    transition: all .3s;
    border-bottom: 1px solid #f0f0f0;
  }
  tbody{
    tr:hover td{
      background: #eee;
      border-color: #eee;
    }
  }
}


@media screen and (max-width: 992px){
  .sizes-page{
    .m-title{
      max-width: 16.75em;
      svg{
        margin-left: -8em;
      }
    }
    .about-top__desc{
      padding-bottom: 0;
    }
  }

  .size-info{
    padding: 1.875em 0;
    &__inner{
      align-items: flex-start;
      grid-template-columns: calc(100% - 22.125em) 20.625em;
    }
    &__col3{
      display: none;
    }
    &__img{
      width: 100%;
      display: block;
    }
    .text{
      h4{
        font-size: 1em;
      }
    }
  }

  .size-info2{
    background-image: unset;
    padding: 1.875em 0;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .sizes-table{
    th{
      font-size: .875em;
      line-height: 114%;
      padding: 1.28em .125em;
    }
    td{
      font-size: 1em;
      line-height: 160%;
      padding: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .sizes-page{
    .m-arrows{
      width: 11em;
      top: 1.5em;
      row-gap: 1em;
      transform: translateX(1em);
    }
  }

  .size-info{
    &__inner{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__col3{
      display: block;
      order: -1;
    }
  }

  .sizes-table{
    th{
      br{
        display: unset;
      }
    }
  }
}
