.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: 1.0625em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #E4E4E4;
    border: none;
    box-shadow: none;
    height: .1875em;
    border-radius: 0;
    .noUi-connect{
      background-color: $black;
    }
    .noUi-handle{
      box-shadow: none;
      border: .1875em solid $black;
      background-color: $white;
      width: 1.0625em;
      height: 1.0625em;
      top: -.4375em !important;
      right: -.4375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.875em;
    &-title{
      position: absolute;
      top: 50%;
      left: 1.25em;
      transform: translateY(-50%);
    }
  }
  .fg{
    position: relative;
    &__title,
    &__currency{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    &__title{
      font-size: .875em;
      left: .33em;
      color: #A4A4A4;
    }
    &__currency{
      right: .33em;
      color: #515151;
      font-size: .9375em;
    }
    input{
      text-align: center;
      height: 2.75em;
      padding: 0 1.5em;
    }
  }
}
