.gallery{
  position: absolute;
  width: calc(100% - 44.3125em);
  left: 0;
  top: 0;
  display: grid;
  grid-template-columns: 9.625em calc(100% - 10.875em);
  gap: .625em 1.25em;
  .favourite-btn{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
    z-index: 2;
    &:not(._added){
      path{
        stroke: $black;
      }
    }
  }
  &__img{
    @include imgCover(93.5%);
    min-height: 100%;
    &--video{
      &:after{
        content: '';
        position: absolute;
        right: .625em;
        bottom: .625em;
        width: 1.875em;
        height: 1.875em;
        background-image: url(../img/play.svg);
        @include bgImgSize(100%);
      }
    }
  }
  &__big{
    position: relative;
    height: 50em;
    cursor: pointer;
    .swiper{
      height: 100%;
      &-nav{
        position: absolute;
        right: 1.25em;
        bottom: 1.25em;
      }
      &-button{
        background-color: $white;
      }
    }
    .gallery__img--video:after{
      right: unset;
      left: .625em;
      display: none;
    }
  }
  &__small{
    height: 50em;
    .swiper{
      height: calc(50em + 1.25em);
      &-slide{
        height: 20%;
        padding-bottom: 1.25em;
      }
    }
    .gallery__img{
      cursor: pointer;
    }
  }
  .swiper-button-disabled{
    pointer-events: unset;
  }
}

.sizes,
.colors{
  margin-top: 1.5625em;
  &__title,
  &__link{
    font-size: .9375em;
    line-height: 120%;
  }
  &__title{
    color: #5A5A5A;
  }
}

.sizes{
  &__link{
    text-decoration: underline;
  }
  &__items{
    margin-top: .875em;
    display: grid;
    grid-template-columns: repeat(10,calc(10% - 2.8125em / 10));
    gap: .3125em;
  }
}

.size-item{
  height: 2.5em;
  label{
    height: 100%;
    border: 1px solid #E2E2E2;
    @include center;
    color: #302F2F;
  }
  input:checked{
    &~label{
      background-color: #1E1E1E;
      border-color: #1E1E1E;
      color: $white;
    }
  }
}

.colors{
  &__items{
    margin-top: .625em;
    display: grid;
    grid-template-columns: repeat(9,calc(100% / 9 - 5em / 9));
    gap: .625em;
  }
}

.color-item{
  flex-shrink: 0;
  label{
    @include imgCover(100%);
    &:after{
      content: '';
      @include centerAbsolute;
      width: calc(100% + .5em);
      height: calc(100% + .5em);
      border: 1px solid $black;
      opacity: 0;
      transition: all .3s;
    }
  }
  input:checked{
    &~label:after{
      opacity: 1;
    }
  }
}

.product-cart{
  margin-top: 1.5625em;
  &__prices{
    display: flex;
    align-items: center;
  }
  &__price{
    font-size: 1.6875em;
  }
  &__price2{
    font-size: 1.1875em;
    color: rgba($black,.3);
    text-decoration: line-through;
    margin-left: .5em;
  }
  &__bottom{
    margin-top: 1.5625em;
    @include grid100gap(.6875em);
    width: 21.125em;
  }
  &__btn2{
    display: none;
  }
}

.product-partial{
  padding: .625em;
  border-radius: .125em;
  border: 1px solid #D8D8D8;
  display: flex;
  align-items: center;
  &__img{
    width: 4.875em;
    margin-right: .625em;
  }
  &__arrow{
    width: .5em;
    margin-left: auto;
  }
}

.product-faq{
  margin-top: 1.5625em;
  @include grid100gap(.4375em);
}

.qa-card{
  border-bottom: 1px solid #DBDBDB;
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .6875em 0;
  }
  &__title{
    font-size: .8125em;
    text-transform: uppercase;
    font-weight: 600;
    color: #383838;
  }
  &__btn{
    opacity: .3;
    width: .625em;
    @include center;
    svg{
      width: 100%;
    }
  }
  &__hidden{
    padding-bottom: .6875em;
    color: #656565;
    display: none;
  }
  &._active{
    .qa-card__btn{
      opacity: 1;
      transform: rotate(-180deg);
      path{
        fill: $black;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .gallery{
    width: 22.5em;
    grid-template-columns: 100%;
    &__big{
      height: auto;
    }
    &__small{
      height: auto;
      order: 2;
      .swiper{
        height: auto;
        width: calc(100% + .625em);
        &-slide{
          width: calc(100% / 3);
          padding: 0 .625em 0 0;
        }
      }
    }
    .swiper{
      &-button{
        width: 2em;
        height: 2em;
        background-color: transparent;
        border: none;
      }
    }
  }

  .sizes{
    &__items{
      grid-template-columns: repeat(6,calc(100% / 6 - 1.5625em / 6));
    }
  }

  .size-item{
    height: 2.0625em;
    label{
      font-size: .875em;
    }
  }

  .colors{
    &__items{
      grid-template-columns: repeat(5,calc(20% - .5em));
    }
  }

  .product-cart{
    &__bottom{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .gallery{
    order: 2;
    left: unset;
    top: unset;
    position: relative;
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    &__small{
      display: none;
    }
  }

  .colors{
    order: 3;
    &__items{
      grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
    }
  }

  .sizes{
    order: 5;
  }

  .product-cart{
    order: 4;
    &__btn{
      display: none;
    }
    &__btn2{
      display: flex;
      width: 100%;
      margin-top: 1.5em;
    }
  }

  .product-faq{
    order: 6;
  }
}
