.order-thank{
  padding: 3.125em 0 8.125em;
  &__inner{
    width: 71em;
    max-width: 100%;
    margin: 0 auto;
  }
  .h1{
    text-align: center;
  }
  &__desc{
    margin-top: 2.5em;
    @include center;
    img{
      width: 2em;
      margin-right: .625em;
    }
  }
  &__content{
    border: .1875em solid #F8F8F8;
    padding: 1.6875em 1.0625em 1.0625em;
  }
  &__items{
    @include grid100gap(.625em);
  }
  &__props{
    margin-top: 1.875em;
    border-top: 1px dashed #E9E9E9;
    padding-top: 1.25em;
    @include grid100gap(1.0625em);
    .prop-item{
      &__title{
        font-size: .9375em;
        line-height: 140%;
      }
      &__desc{
        font-size: 1.0625em;
        line-height: 123%;
      }
    }
  }
  &__bottom{
    padding: 2.5em 1.25em;
    background-color: #F8F8F8;
    .prop-item{
      &__title{
        font-size: 1.1875em;
        line-height: 110%;
        font-weight: 500;
      }
      &__desc{
        font-size: 1.5625em;
        line-height: 100%;
        font-weight: 500;
      }
    }
  }
}

.order-props{
  margin-top: 2.5em;
  padding: 2.5em;
  background-color: #F8F8F8;
  display: grid;
  grid-template-columns: repeat(4,25%);
  &__title{
    opacity: .4;
    font-size: .875em;
    line-height: 129%;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 144%;
    margin-top: .39em;
    font-weight: 500;
  }
}

.order-card{
  display: grid;
  grid-template-columns: 8em calc(100% - 9em);
  gap: 1em;
  align-items: center;
  &__img{
    @include imgCover(92%);
  }
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 27.5em) 19em 8.5em;
    align-items: center;
  }
  &__content{
    @include grid100gap(.375em);
  }
  .cart-card__title{
    font-size: 1.125em;
  }
  .cart-card__prices{
    justify-content: space-between;
  }
  .cart-card__count{
    position: static;
  }
}


@media screen and (max-width: 992px){
  .order-thank{
    padding: 1.875em 0;
    .h1{
      max-width: 8em;
      margin: 0 auto;
    }
    &__desc{
      margin-top: .625em;
    }
    &__content{
      padding: 1.0625em;
    }
    &__items{
      gap: 1em;
    }
    &__bottom{
      padding: 1.25em;
      .prop-item__desc{
        font-size: 1.25em;
      }
    }
  }

  .order-props{
    padding: .9375em 1.25em;
    margin-top: 1.875em;
    &__desc{
      font-size: 1em;
      margin-top: .4375em;
    }
  }

  .order-card{
    grid-template-columns: 5.5em calc(100% - 6.5em);
    &__wrap{
      grid-template-columns: calc(100% - 8.5em) 8.5em;
      position: relative;
    }
    &__content{
      padding-top: 1.3125em;
    }
    .cart-card__articul{
      position: absolute;
      top: 0;
      left: 0;
      font-size: .8125em;
    }
    .cart-card__prices{
      display: flex;
    }
    .cart-card__title{
      font-size: 1.0625em;
    }
    .cart-card__color,
    .cart-card__size{
      font-size: .9375em;
    }
    .cart-card__price{
      font-size: 1.1875em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-thank{
    &__desc{
      flex-direction: column;
      text-align: center;
      img{
        margin: 0 0 .3125em;
      }
    }
    &__content{
      padding: .4375em;
    }
  }

  .order-props{
    padding: .9375em;
    grid-template-columns: 100%;
    gap: .625em;
  }

  .order-card{
    align-items: flex-start;
    &__wrap{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__img{
      margin-top: .3125em;
    }
    .cart-card__prices{
      justify-content: unset;
    }
    .cart-card__count{
      margin-right: 1em;
    }
  }
}
