.orders{
  @include grid100gap(1.875em);
  margin-top: 1.875em;
  &:first-child{
    margin-top: 0;
  }
  .empty-search{
    margin-top: 1.25em;
  }
}

.orders-list{
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
}

.order-item{
  &._open{
    .order-header__btn{
      span{
        opacity: 0;
      }
      img{
        transform: rotate(-90deg);
      }
    }
  }
  &__content{
    display: none;
  }
  .order-thank__content{
    padding: 1.25em;
    border-color: #F1F1F1;
  }
  .prop-item{
    &._sum{
      .prop-item__title{
        font-size: 1.1875em;
        font-weight: 500;
        line-height: 100%;
      }
      .prop-item__desc{
        font-size: 1.5625em;
        font-weight: 500;
        line-height: 100%;
      }
    }
  }
}

.order-header{
  padding: .625em 1.25em;
  background-color: #F4F4F4;
  display: grid;
  grid-template-columns: 14em calc(100% - 61em) 20em 20em 7em;
  &__col{
    display: flex;
    align-items: center;
  }
  &__title{
    display: none;
    font-size: .9375em;
    &._show-desktop{
      display: block;
      margin-right: .67em;
    }
  }
  &__btn{
    margin-left: auto;
    display: flex;
    align-items: center;
    font-weight: 500;
    &:hover{
      color: $primary;
    }
    span{
      font-size: .875em;
    }
    img{
      width: 1.75em;
      margin-left: .625em;
      transition: all .3s;
    }
    ._view2{
      display: none;
    }
  }
}

.m-status{
  @include center;
  border: 1px solid $primary;
  padding: 0 .5em;
  height: 2em;
  color: $primary;
  font-weight: 500;
  min-width: 7.25em;
  &._green{
    color: #52B461;
    border-color: #52B461;
  }
  &._red{
    color: #F31010;
    border-color: #F31010;
  }
  img{
    width: 1em;
    margin-right: .375em;
  }
  span{
    font-size: .8125em;
  }
}


@media screen and (max-width: 992px){
  .orders-list{
    &__items{
      margin-top: .9375em;
      gap: .9375em;
    }
  }

  .order-header{
    grid-template-columns: 100%;
    gap: .625em;
    padding: .625em 0 0;
    background-color: transparent;
    border: 1px solid #F1F1F1;
    &__col{
      padding-left: .9375em;
      padding-right: .9375em;
      justify-content: space-between;
      &:last-child{
        padding-top: .625em;
        padding-bottom: .625em;
        justify-content: center;
        background-color: #F1F1F1;
      }
    }
    &__title{
      display: block;
    }
    &__btn{
      margin: 0;
    }
  }

  .order-item{
    &._open{
      .order-header__btn{
        span{
          opacity: 1;
        }
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    .order-thank__content{
      padding: .625em;
    }
    .prop-item{
      &._sum{
        padding: 1.25em;
        width: calc(100% + 1.25em);
        margin: 0 0 -.625em -.625em;
        background-color: #F1F1F1;
        .prop-item__desc{
          font-size: 1.25em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  ._hide-mob{
    display: none;
  }
}
