.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1em;
    line-height: 119%;
    margin-bottom: .3125em;
    span{
      color: $red;
    }
  }
  input,
  textarea{
    border-radius: 0;
    border: 1px solid #f4f4f4;
    padding: 0 1.07em;
    height: 3.93em;
    width: 100%;
    font-size: .875em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: #a4a4a4;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $red;
      color: $red;
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .0625em;
    color: $red;
    display: none;
    font-size: .875em;
    line-height: 150%;
  }
  &--type2{
    textarea,
    input{
      font-size: 1em;
      line-height: 150%;
      padding: 0 .625em;
      height: 3.5375em;
      border: none;
      border-bottom: 1px solid $black;
    }
    textarea{
      padding: 1em .625em;
      height: 5.5em;
    }
  }
}

.m-select{
  label{
    font-size: .875em;
    line-height: 120%;
    color: #5a5a5a;
    display: block;
    margin-bottom: .71em;
  }
  .nice-select{
    font-size: 1em;
    width: 100%;
    border: 1px solid #eee;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0;
    white-space: nowrap;
    height: 2.875em;
    padding: 0 .9375em;
    &.open{
      &:before{
        transform: rotate(180deg);
        opacity: 1;
      }
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .625em;
      height: .625em;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='9' height='10' viewBox='0 0 9 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 2.75L4.50002 7.25002L9 2.75H0Z' fill='black'/%3E%3C/svg%3E");
      @include bgImgSize(100%);
      margin-left: .375em;
      opacity: .25;
    }
    .current{
      font-size: .875em;
      line-height: 157%;
    }
    .list{
      border-top: none;
      border: 1px solid #eee;
      box-shadow: none;
      margin-top: 0;
      border-radius: 0;
      width: calc(100% + 2px);
      left: -1px;
      background: #FFF;
      max-height: 8.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 3.28em;
      font-size: .875em;
      line-height: 157%;
      padding: 0 1.07em;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: #ededed;
        font-weight: 400;
        color: $black;
      }
      &.selected{
        display: none;
      }
    }
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1em;
  width: 1.5em;
  height: 1.5em;
  @include center;
  img,
  svg{
    width: 100%;
    @include centerAbsolute;
    transition: all .3s;
  }
  ._view1{
    opacity: .5;
  }
  ._view2{
    opacity: 0;
  }
  &._active{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
}

.favourite-filter__item,
.order-delivery__tab,
.radio-field,
.color-item,
.size-item,
.file-input__btn,
.m-radio,
.m-check{
  position: relative;
  &>input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.file-input{
  display: flex;
  &__col{
    margin-right: 1em;
  }
  &__btn{
    width: 2.75em;
    height: 2.75em;
    position: relative;
    overflow: hidden;
    &:before,
    &:after{
      content: '';
      @include centerAbsolute;
      @include bgImgSize(100%);
      transition: all .3s;
    }
    &:before{
      width: 1.5em;
      height: 1.5em;
      background-image: url(../img/clip.svg);
    }
    &:after{
      opacity: 0;
      width: 1.125em;
      height: 1.125em;
      background-image: url(../img/plus.svg);
    }
    input{
      font-size: 200px;
      height: auto;
      width: auto;
      right: 0;
      left: unset;
    }
  }
  &__text{
    padding: .0625em 0;
  }
  &__title{
    line-height: 119%;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.5em;
    .file-item{
      margin: .5em 1.5em 0 0;
    }
  }
  &._uploaded{
    .file-input__btn{
      &:before{
        opacity: 0;
      }
      &:after{
        opacity: 1;
      }
    }
    .file-input__text{
      display: none;
    }
  }
}

.file-item{
  display: flex;
  align-items: center;
  &__format{
    background: #f1f1f1;
    width: 3em;
    height: 2.75em;
    @include center;
    text-transform: uppercase;
    font-weight: 600;
    margin-right: .5em;
  }
  &__close{
    width: .875em;
    @include center;
    margin: 0 0 0 .5em;
    opacity: .4;
    &:hover{
      opacity: 1;
    }
    svg{
      width: 100%;
    }
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.125em;
    min-height: 1.5em;
    span{
      font-size: .9375em;
      line-height: 140%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid #6a6a6a;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._small{
    label{
      &:before{
        top: 0;
        transform: unset;
      }
      span{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $black;
        border-color: $black;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      background-image: url(../img/check2.svg);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #e9edf2;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #e9edf2;
    border-right: 1px solid #e9edf2;
    color: $black;
    font-weight: 500;
    font-size: 1.0625em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    background-color: transparent;
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}


