.mistake {
  position: relative;
  flex-grow: 1;
  padding-bottom: 8.125em;
  overflow: hidden;
  .m-arrows{
    align-self: flex-end;
  }
  .m-title{
    &--arrow svg {
      top: unset;
      bottom: 0;
      margin-left: -10em;
    }
  }
  &__group {
    max-width: 36.75em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 2.5em;
    margin: 0 auto;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 31.875em;
    margin: 0 auto;
    p {
      font-size: 1em;
      line-height: 130%;
      color: #343434;
    }
  }
  &__img2 {
    position: absolute;
    left: 0;
    bottom: -12em;
    width: 28.875em;
    pointer-events: none;
    img:nth-child(2),
    img:nth-child(3){
      display: none;
    }
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .mistake {
    padding-bottom: 1.875em;
    .m-title--arrow svg {
      margin-left: -4em;
    }
    &__group {
      max-width: 26.375em;
      gap: 1.5em;
    }
    &__img {
      max-width: 20.625em;
    }
    &__text {
      p {
        font-size: 0.875em;
      }
    }
    &__img2 {
      bottom: -2em;
      width: 7.75em;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    .m-arrows{
      top: 1.125em;
    }
    &__img2 {
      width: 2.3125em;
      bottom: -4.5em;
      img:nth-child(2) {
        display: none;
      }
      img:nth-child(3) {
        display: block;
      }
    }
  }
}
