.cart{
  padding: 3.125em 0 4.125em;
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__title{
    max-width: 6em;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: calc(100% - 27.75em) 25.25em;
    gap: 1.875em 2.5em;
  }
  &__top2{
    border-bottom: 1px solid #e4e4e4;
    padding-bottom: .625em;
    .m-check{
      label{
        text-transform: uppercase;
        font-weight: 500;
        span{
          font-size: .6875em;
          line-height: 218%;
        }
      }
    }
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}

.delete-btn{
  border-bottom: 1px dashed $black;
  font-weight: 500;
  font-size: .6875em;
  line-height: 164%;
  text-transform: uppercase;
  color: #373737;
  &:hover{
    color: $primary;
    border-color: $primary;
  }
}

.cart-card{
  padding-left: 2.75em;
  display: grid;
  grid-template-columns: 13.875em calc(100% - 46.6875em) 30.3125em;
  gap: 1.25em;
  align-items: center;
  position: relative;
  .m-check{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &__img{
    @include imgCover(92.3%);
  }
  &__content{
    @include grid100gap(.625em);
  }
  &__title{
    font-size: 1.375em;
    line-height: 118%;
  }
  &__articul,
  &__color,
  &__size{
    font-size: 1em;
    line-height: 125%;
  }
  &__prices{
    display: flex;
  }
  &__price,
  &__price2{
    font-size: 1.25em;
    line-height: 105%;
    text-transform: uppercase;
  }
  &__price2{
    margin-left: .5em;
    opacity: .4;
    text-decoration: line-through;
  }
  &__sum{
    font-size: 1.4375em;
    line-height: 91%;
    text-transform: uppercase;
  }
  &__delete{
    width: 2.75em;
    height: 2.75em;
    padding: 0;
    border-radius: 50%;
    svg{
      width: 1.125em;
    }
    &:after{
      display: none;
    }
    &:hover{
      background: #E4E4E4;
      path{
        stroke: $black;
      }
    }
  }
  &__count{
    padding: 0 .375em;
    background: #f4f4f4;
    border-radius: .1875em;
    @include center;
    height: 1.25em;
    font-weight: 500;
  }
}

.cart-sum{
  @include grid100gap(1.5625em);
  background: #f8f8f8;
  padding: 1.5625em;
  &__title{
    font-size: 1.75em;
    line-height: 157%;
    font-weight: 500;
  }
  &__props{
    @include grid100gap(1.5625em);
  }
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
  &__title{
    &:not(.p-text){
      color: #2e3036;
    }
    &._big{
      font-size: 1.375em;
      line-height: 100%;
    }
  }
  &__desc{
    font-size: 1.0625em;
    order: 2;
    &._big{
      font-size: 1.375em;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 175%;
  }
}

.cart-empty{
  margin: 2.1875em auto 0;
  width: 50.5em;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    width: 13.5em;
  }
  &__text{
    margin-top: .625em;
    @include grid100gap(.3125em);
  }
  &__desc{
    font-size: 1.125em;
    line-height: 156%;
    opacity: .7;
    a{
      font-weight: 700;
    }
    p{
      line-height: inherit;
    }
  }
  &__link{
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding: 1.875em 0;
    &__arrows{
      width: 9.5em;
      overflow: hidden;
      .m-arrows{
        row-gap: 1.25em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.875em;
    }
    &__top2{
      padding-left: 1.75em;
      padding-right: 1.75em;
    }
    &__col{
      width: calc(100% + 3.5em);
      margin-left: -1.75em;
    }
    &__items{
      margin: 0;
      gap: 0;
    }
    .m-check{
      label{
        min-height: 1.25em;
        &:before{
          width: 1.25em;
          height: 1.25em;
        }
      }
    }
  }

  .cart-card{
    grid-template-columns: 5.25em calc(100% - 26.75em) 20.25em;
    gap: .625em;
    padding: .9375em 1.75em .9375em 3.625em;
    border-bottom: 1px solid #e4e4e4;
    .m-check{
      left: 1.75em;
    }
    &__content{
      gap: .25em;
    }
    &__title{
      font-size: 1.0625em;
    }
    &__color,
    &__size{
      font-size: .875em;
    }
    &__prices{
      display: none;
    }
    .cartcalc{
      height: 2.5em;
      width: 6.625em;
    }
    &__sum{
      font-size: 1.1875em;
    }
  }

  .cart-empty{
    margin-top: 3.75em;
    &._mt0{
      margin-top: 0;
    }
    &__img{
      width: 6.25em;
    }
    &__text{
      gap: .625em;
    }
    &__desc{
      font-size: .875em;
      line-height: 136%;
      @include grid100gap(1em);
    }
  }
}


@media screen and (max-width: 576px){
  .cart{
    &__arrows{
      width: 4.75em;
      .m-arrows{
        row-gap: 1.125em;
      }
    }
    &__top2{
      padding-left: .9375em;
      padding-right: .9375em;
    }
    &__col{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
  }

  .cart-card{
    grid-template-columns: 5.25em calc(100% - 5.875em);
    padding: .9375em .9375em .9375em 2.8125em;
    .m-check{
      top: 2.75em;
      left: .9375em;
      transform: unset;
    }
    &__action{
      grid-column: 1/-1;
    }
  }
}
