.h1{
  font-size: 5.75em;
  line-height: 117%;
}

.h2{
  font-size: 3.5em;
  line-height: 118%;
}

.text h3,
.h3{
  font-size: 1.6875em;
  line-height: 119%;
}

.text h4,
.h4{
  font-size: 1.25em;
  line-height: 125%;
}

.h5{
  font-size: 1em;
  line-height: 137%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 131%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text li,
.text p,
.text16{
  font-size: 1em;
  line-height: 162%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 136%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.caps-text{
  text-transform: uppercase;
}

.orange-text{
  color: $primary;
}

.blue-text{
  color: $secondary;
}

.gray-text{
  color: $gray;
}

.black70{
  color: rgba($black,.7);
}

.black60{
  color: rgba($black,.6);
}



@media screen and (max-width: 992px){
  .h1{
    font-size: 2.0625em;
  }

  .h2{
    font-size: 1.8125em;
  }

  .h3{
    font-size: 1.3125em;
  }

  .h4{
    font-size: .9375em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
