.team-top{
  padding: 2.5em 0 4.375em;
  &__inner{
    position: relative;
  }
  &__arrows{
    position: absolute;
    left: 5.625em;
    bottom: -1.875em;
  }
  &__text{
    position: relative;
    z-index: 2;
  }
  &__title{
    @include grid100gap(.625em);
    .h1{
      &:nth-child(2){
        padding-left: 2.27em;
      }
    }
  }
  &__desc{
    margin-top: 1.875em;
    max-width: 17.125em;
  }
  &__img{
    padding-left: 19.125em;
    margin-top: -11em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.team-group{
  padding: 4.375em 0;
  &__top{
    padding: .625em 0;
    border-bottom: 1px solid #e2e2e2;
  }
  &__inner{
    margin-top: 4.625em;
    width: 102.5em;
    max-width: 100%;
  }
  &__content{
    position: relative;
  }
  &._type2{
    @media screen and (min-width: 993px){
      .team-slider{
        .swiper-slide:nth-child(5){
          display: block;
          grid-column: unset;
        }
        .swiper-slide:nth-child(3),
        .swiper-slide:nth-child(4){
          grid-column: span 2;
          display: grid;
          grid-template-columns: repeat(2,calc(50% - .625em));
          gap: 1.25em;
        }
      }
    }
  }
}

.team-info{
  position: absolute;
  top: 0;
  left: 0;
  width: calc(20% - 1em);
  height: 23.125em;
  border: .25em solid #EEEEEE;
  padding: 1.9375em;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  &__title{
    font-weight: 600;
    max-width: 12.25em;
    .text18{
      line-height: 139%;
    }
  }
  &__desc{
    font-size: .8125em;
    line-height: 169%;
    color: #8C8C8C;
    font-weight: 500;
  }
}

.team-slider{
  @media screen and (min-width: 993px){
    .swiper-wrapper{
      display: grid;
      grid-template-columns: repeat(5,calc(20% - 1em));
      gap: 2.5em 1.25em;
      transform: unset !important;
      &:before{
        content: '';
      }
    }
    .swiper-slide:nth-child(5){
      grid-column: span 2;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }
}

.team-card{
  &__img{
    @include imgCover(100%);
  }
  &__content{
    margin-top: .9375em;
    @include grid100gap(.0625em);
  }
  &__name{
    font-size: 1.25em;
    line-height: 105%;
    font-weight: 500;
  }
  &__prof{
    font-size: .9375em;
    line-height: 140%;
    color: #616161;
  }
}

.team-member{
  padding: 8.25em 0;
  &--shops{
    @media screen and (min-width: 993px){
      .team-member__wrap{
        margin-left: 12.5em;
      }
    }
  }
  &__inner{
    padding: 3.125em;
    background-color: #F5F5F5;
    width: calc(100% - 5.75em);
    display: flex;
    align-items: center;
    position: relative;
  }
  &__img{
    position: absolute;
    right: -5.75em;
    bottom: 0;
    width: 33.25em;
  }
  &__title{
    &-item{
      font-size: 2.8125em;
      line-height: 124%;
      text-transform: uppercase;
      &:nth-child(2){
        padding-left: 1.375em;
      }
    }
    span{
      border: .07em solid $primary;
      border-radius: 20em;
      padding: 0 .25em;
    }
  }
  &__wrap{
    margin-left: 6.375em;
  }
  &__nav{
    display: flex;
    margin-top: 1.625em;
  }
  &__desc{
    max-width: 19em;
    opacity: .6;
    margin-top: .625em;
  }
}


@media screen and (max-width: 992px){
  .team-top{
    padding: 1.875em 0;
    &__inner{
      padding: 0 .9375em;
    }
    &__text{
      display: flex;
      align-items: flex-start;
    }
    &__title{
      gap: 0;
      text-align: right;
      margin-right: 1.875em;
      .h1:nth-child(2){
        padding-left: 0;
      }
    }
    &__desc{
      max-width: 20.75em;
      margin: 0;
    }
    &__img{
      margin-top: -1.5em;
      padding-left: 4.75em;
    }
    &__arrows{
      left: 2em;
      bottom: -1.125em;
      width: 4.75em;
      overflow: hidden;
    }
  }

  .team-group{
    padding: 1.875em 0;
    overflow: hidden;
    &__top{
      padding: .625em .9375em;
    }
    &__inner{
      margin-top: 1.25em;
    }
    &__content{
      display: grid;
      grid-template-columns: 13.5em calc(100% - 13.5em - .9375em);
      gap: 1.25em .9375em;
    }
  }

  .team-info{
    height: 100%;
    width: 100%;
    padding: 1em;
    position: relative;
    background-color: $white;
    z-index: 3;
    &:after{
      content: '';
      position: absolute;
      left: 100%;
      top: -.25em;
      width: .75em;
      height: calc(100% + .5em);
      background-color: $white;
      margin-left: .25em;
    }
    &__title{
      max-width: 100%;
      .text18{
        font-size: .875em;
        line-height: 178%;
      }
    }
    &__desc{
      font-size: .75em;
      line-height: 158%;
    }
  }

  .team-slider{
    .swiper{
      &-slide{
        width: 14.4375em;
        padding-right: .9375em;
      }
    }
  }

  .team-card{
    &__content{
      margin-top: .625em;
    }
    &__name{
      font-size: 1.0625em;
    }
    &__prof{
      font-size: .8125em;
    }
  }

  .team-member{
    padding: 1.875em 0;
    &__inner{
      padding: 1.25em;
      width: calc(100% - 2.25em);
      display: block;
    }
    &__col{
      position: relative;
      display: inline-block;
    }
    &__img{
      width: 18em;
      right: -2.25em;
    }
    &__title{
      &-item{
        font-size: 1.5em;
        &:nth-child(2){
          padding-left: 1.0625em;
        }
      }
    }
    &__wrap{
      margin: 1.25em 0 0;
      display: flex;
      align-items: center;
    }
    &__arrows{
      width: 10.3125em;
      height: 2.3125em;
      overflow: hidden;
    }
    &__nav{
      margin: 0 0 0 2em;
    }
    &__desc{
      margin-top: .875em;
    }
    @media screen and (min-width: 577px){
      &__desc{
        width: 13em;
        position: absolute;
        left: 100%;
        margin: 0 0 0 1.625em;
        top: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .team-top{
    padding-top: 1.5625em;
    &__inner{
      padding: 0 0 3.5em;
    }
    &__text{
      flex-direction: column;
      position: static;
    }
    &__title{
      position: relative;
      z-index: 2;
    }
    &__img{
      padding: 0;
    }
    &__desc{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      margin: 0;
    }
    &__arrows{
      bottom: unset;
      top: -1em;
      left: unset;
      right: 0;
    }
  }

  .team-group{
    &__top{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
    &__inner{
      margin-top: .625em;
    }
    &__content{
      grid-template-columns: 100%;
    }
  }

  .team-info{
    height: unset;
    padding: 0;
    border: none;
    display: block;
    &:after{
      display: none;
    }
  }

  .team-member{
    padding-bottom: 4.5em;
    &__inner{
      width: 100%;
      padding-bottom: 10.875em;
    }
    &__img{
      right: 0;
      width: 100%;
      bottom: -2.625em;
    }
    &__title{
      padding-left: .625em;
    }
    &__wrap{
      flex-direction: column;
      margin-top: 2em;
    }
    &__arrows{
      width: 100%;
      height: auto;
      .m-arrows{
        width: 100%;
      }
    }
    &__nav{
      margin: 2em 0 0;
    }
  }
}
