.shops{
  padding-bottom: 3.75em;
  .about-top__inner{
    align-items: flex-end;
  }
  .m-title{
    max-width: 36em;
    svg{
      top: unset;
      bottom: 0;
      margin-left: -3em;
    }
  }
  .about-top__desc{
    padding-bottom: .75em;
  }
  .m-arrows{
    align-self: unset;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
}

.shops-filter{
  &__inner{
    display: flex;
  }
}

.city-select{
  display: flex;
  align-items: center;
  &__title{
    font-size: .875em;
    margin-right: .7em;
    white-space: nowrap;
  }
  .m-select{
    min-width: 18.75em;
  }
}

.shops-content{
  margin-top: 3.75em;
}

.shop-card{
  border: .1875em solid #EEEEEE;
  padding: 2em;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  text-transform: uppercase;
  &__img{
    height: 7.5em;
    display: flex;
    align-items: center;
    margin-bottom: 2.5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 167%;
    font-weight: 600;
  }
  &__city{
    font-size: .75em;
    line-height: 120%;
    color: #7C7C7C;
    margin-top: .8em;
  }
  &__site{
    font-size: .75em;
    line-height: 120%;
    color: $primary;
    margin-top: .8em;
    text-decoration: underline;
    word-break: break-word;
  }
}


@media screen and (max-width: 992px){
  .shops{
    padding-bottom: 1.875em;
    .m-title{
      max-width: 12.5em;
      svg{
        margin-left: -.5em;
      }
    }
    .about-top__desc{
      padding-bottom: 0;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    }
  }

  .city-select{
    &__title{
      font-size: .875em;
    }
    .m-select{
      min-width: 16.5em;
    }
  }

  .shops-content{
    margin-top: 1.25em;
  }

  .shop-card{
    padding: 1.375em;
  }
}


@media screen and (max-width: 576px){
  .shops{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .city-select{
    width: 100%;
    .m-select{
      min-width: unset;
      flex-grow: 1;
    }
  }

  .shop-card{
    border-width: 2px;
    padding: .75em;
    &__img{
      height: 5em;
      margin-bottom: .5em;
    }
    &__title{
      font-size: .875em;
    }
    &__city,
    &__site{
      margin-top: .5em;
    }
  }
}
