.order{
  padding-bottom: 8.125em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 37.875em) 35.375em;
    gap: 1.875em 2.5em;
    margin-top: 2.5em;
  }
  .cart-sum{
    padding: 1.875em;
    .m-check{
      color: rgba($black,.7);
    }
  }
}

.order-cart{
  padding: .625em;
  @include grid100gap(.625em);
  background-color: $white;
  .cart-card{
    padding: 0;
    width: 100%;
    grid-template-columns: 8em calc(100% - 9.25em);
    &__content{
      gap: .375em;
      position: relative;
    }
    &__title{
      font-size: 1.25em;
      padding-right: .5em;
    }
    &__color,
    &__size{
      font-size: .875em;
    }
    &__count{
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}

.order-tabs{
  margin-bottom: 2.5em;
  display: flex;
  border-bottom: 1px solid #E4E4E4;
}

.order-tab{
  height: 3.75em;
  margin-bottom: -1px;
  background-color: $white;
  border: 1px solid transparent;
  border-bottom-color: #E4E4E4;
  @include center;
  padding: 0 1.5625em;
  cursor: pointer;
  color: rgba($black,.6);
  transition: all .3s;
  font-weight: 500;
  &._active{
    pointer-events: none;
    border-color: #E4E4E4;
    border-bottom-color: transparent;
    color: $black;
    span{
      border-color: transparent;
    }
  }
  &:hover{
    cursor: pointer;
    color: $black;
  }
  span{
    font-size: 1em;
    line-height: 125%;
    border-bottom: 1px dashed #828282;
  }
  ._view2{
    display: none;
  }
}

.order-group{
  margin-top: 2.5em;
  &:first-child{
    margin-top: 0;
  }
  &__top{
    margin-bottom: 1.5625em;
    display: flex;
    align-items: center;
    padding-bottom: .75em;
    border-bottom: 1px solid #e4e4e4;
  }
  &__num{
    width: 1.875em;
    height: 1.875em;
    border-radius: 50%;
    flex-shrink: 0;
    @include center;
    background-color: $black;
    color: $white;
    margin-right: .75em;
    font-weight: 600;
    span{
      font-size: .875em;
    }
  }
  &__title{
    font-weight: 500;
    font-size: .9375em;
    text-transform: uppercase;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 7.8125em / 5));
    gap: 1.5625em;
    grid-auto-flow: dense;
    .fg{
      grid-column: span 2;
    }
    ._big{
      grid-column: span 3;
    }
  }
}


@media screen and (max-width: 992px){
  .order{
    padding-bottom: 1.875em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.875em;
    }
    .cart-sum{
      padding: 1.25em;
      gap: .625em;
      &__title{
        font-size: 1.3125em;
        line-height: 143%;
        font-weight: 400;
      }
    }
  }

  .order-cart{
    gap: .9375em;
    .cart-card{
      border: none;
      align-items: flex-start;
      grid-template-columns: 5.25em calc(100% - 6.5em);
      &__title{
        font-size: 1.0625em;
        padding-right: 0;
      }
      &__prices{
        display: flex;
      }
      &__count{
        position: static;
        margin-left: .375em;
      }
    }
  }

  .order-tab{
    width: 50%;
  }
}


@media screen and (max-width: 576px){
  .order{
    .cart-sum{
      padding: .625em;
    }
  }

  .order-cart{
    .cart-card{
      &__count{
        position: static;
        margin-left: .375em;
      }
      &__prices{
        display: flex;
      }
      &__price{
        font-size: 1.1875em;
      }
    }
  }

  .order-tab{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  .order-group{
    &__fields{
      grid-template-columns: 100%;
      .fg{
        grid-column: span 1;
      }
      ._big{
        grid-column: span 1;
      }
    }
  }
}
