.catalog-collection2{
  @media screen and (min-width: 993px){
    margin-top: 4em;
    &:last-child{
      padding-bottom: 8.125em;
    }
    .h2{
      font-size: 2.8125em;
    }
  }
  .m-title{
    svg{
      width: 11.25em;
    }
  }
}

.catalog-item{
  padding: 3.125em 0 4em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 44.3125em) 41.1875em;
    gap: 3.125em;
    min-height: 50em;
    position: relative;
  }
  &__col2{
    display: flex;
    flex-direction: column;
  }
  &__top{
    @include grid100gap(.3125em);
  }
  &__subtitle{
    font-size: 1.0625em;
    line-height: 118%;
    color: #646464;
  }
}

.product-images{
  margin-top: 3.125em;
  @media screen and (min-width: 577px){
    .swiper-wrapper{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1.5625em));
      gap: 3.125em;
    }
  }
  &__item{
    @include imgCover(100%);
  }
}

.product-desc{
  margin-top: 3.125em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.5625em));
  gap: 3.125em;
  align-items: center;
  &__video{
    position: relative;
    padding-bottom: 100%;
    video{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      object-fit: cover;
    }
  }
  .text{
    li,
    p{
      font-size: 1.1875em;
      line-height: 131%;
      color: #404040;
    }
    p {
      @include lastM(0 0 .73em);
    }
    ul{
      padding-left: .67em;
      gap: .25em;
      li{
        padding-left: .67em;
        &:before{
          content: '•';
          width: auto;
          height: auto;
          background-color: transparent;
          box-shadow: none;
          top: 0;
        }
      }
    }
  }
}



@media screen and (max-width: 992px){
  .catalog-collection2{
    .m-title{
      svg{
        width: 6.8125em;
        top: unset;
        bottom: -.0625em;
        right: 0;
      }
    }
  }

  .catalog-item{
    padding: 0;
    &__grid{
      grid-template-columns: 22.5em calc(100% - 22.5em - .9375em);
      gap: .9375em;
      min-height: 30em;
    }
    &__top{
      gap: .1875em;
    }
    &__subtitle{
      font-size: .9375em;
    }
  }

  .product-images{
    margin-top: 3.375em;
    @media screen and (min-width: 577px){
      .swiper-wrapper{
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 1.25em;
      }
    }
  }

  .product-desc{
    margin-top: 1.25em;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    align-items: flex-start;
    .text{
      h3{
        font-size: 1.375em;
        font-weight: 500;
      }
      li,
      p{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-collection2{
    .m-title{
      max-width: 12em;
    }
  }

  .catalog-item{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
      min-height: unset;
    }
    &__top{
      margin-bottom: .9375em;
      order: 1;
    }
  }

  .product-images{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        padding-right: .625em;
        width: 19.375em;
      }
    }
  }

  .product-desc{
    grid-template-columns: 100%;
  }
}

