.cooperate {
  padding-bottom: 8.125em;
  .m-arrows{
    align-self: flex-end;
  }
  .m-title--arrow svg {
    top: unset;
    bottom: 0;
    margin-left: -9em;
  }
  .about-top__desc {
    max-width: 27.8125em;
    display: flex;
    align-items: flex-end;
    padding: 0 0 2.25em;
  }
  &-bottom {
    border-top: 0.0625em solid #F5F5F5;
    padding-top: 3.125em;
  }
  &__subtitle {
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 500;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 6.875em;
    margin-top: 2.5em;
  }
  &-form {
    width: calc(100% - 47.1875em);
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2em / 2));
    gap: 1.875em;
    &__btn {
      padding: 0 3.75em;
    }
    &__text {
      max-width: 30em;
      a{
        text-decoration: underline;
      }
    }
  }
  &__images {
    position: relative;
    max-width: 40.25em;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 14.5em;
    img {
      width: 100%;
    }
  }
  .fg-flex {
    display: flex;
    align-items: center;
    gap: 1.875em;
  }
  .fg--100 {
    grid-column: 1/3;
  }
}

@media screen and (max-width: 992px) {
  .cooperate {
    padding-bottom: 1.875em;
    .m-title--arrow svg {
      margin-left: -3em;
    }
    .about-top__desc {
      padding: 0;
    }
    &-bottom {
      padding-top: 1.875em;
    }
    &__wrap {
      margin-top: 1.5625em;
    }
    &-form {
      width: 100%;
      gap: 1.25em;
    }
    &__images {
      display: none;
    }
    .fg-flex {
      gap: 1em;
    }
  }
}

@media screen and (max-width: 576px) {
  .cooperate {
    &__wrap {
      flex-direction: column;
      gap: 1.5625em;
    }
    &-form {
      grid-template-columns: 100%;
    }
    &__images {
      display: block;
    }
    &__logo {
      max-width: 7.5em;
    }
    .fg--100 {
      grid-column: unset !important;
    }
    .fg-flex {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
}
