.catalog{
  padding-top: 3.125em;
  .cart-empty__desc{
    gap: 0;
  }
  &--search{
    padding-bottom: 4em;
    .catalog-items{
      margin-top: 2.1875em;
      @media screen and (min-width: 577px){
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      }
    }
    .empty-search{
      width: 50.75em;
      max-width: 100%;
      margin: 0 auto;
    }
  }
  &--favourite{
    .catalog__nav{
      padding-bottom: 0;
    }
    .product-card{
      .favourite-btn{
        opacity: 1;
      }
    }
  }
}

.catalog-top{
  &__row{
    display: flex;
    align-items: center;
    border-bottom: 1px solid #CFCFCF;
  }
  &__filter-btn{
    display: flex;
    align-items: center;
    font-weight: 500;
    margin-right: 3.125em;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 1.375em;
      margin-right: .375em;
    }
  }
  &__row2{
    margin: .625em 0;
  }
}

.catalog-search{
  height: 3.125em;
  flex-grow: 1;
  padding-left: 1.75em;
  position: relative;
  &__img{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 1.375em;
  }
  input{
    width: 100%;
    border: none;
    height: 100%;
    font-size: .875em;
    font-weight: 500;
    &::placeholder{
      color: #D4D4D4;
    }
  }
}

.category-sort{
  margin-right: 3.125em;
  display: flex;
  align-items: center;
  position: relative;
  &__icon{
    width: 1.375em;
    margin-right: .375em;
  }
  &__title{
    font-size: .9375em;
    line-height: 120%;
    font-weight: 500;
  }
  .nice-select{
    padding-left: .25em;
    padding-right: 0;
    height: auto;
    border: none;
    position: static;
    &:after{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      right: 0;
      top: 0;
      transform: unset !important;
    }
    .current{
      font-size: .9375em;
      color: #515151;
      font-weight: 500;
    }
    .option{
      display: flex !important;
    }
    .list{
      width: auto;
      min-width: 100%;
      margin-top: .375em;
    }
  }
}

.selected-filters{
  padding: 0 0 .625em;
  display: flex;
  flex-wrap: wrap;
  &__item{
    display: flex;
    align-items: center;
    margin: .625em 1.25em 0 0;
    span{
      font-size: .9375em;
    }
    &-remove{
      width: .875em;
      @include center;
      opacity: .3;
      margin-left: .25em;
      &:hover{
        opacity: 1;
      }
      svg{
        width: 100%;
      }
    }
  }
  &__clear{
    margin-top: .625em;
    padding: 0 .625em;
    text-transform: unset;
    height: 2em;
    font-weight: 500;
    span{
      font-size: .9375em;
    }
  }
}



@media screen and (max-width: 992px){
  .catalog{
    padding-top: 1.875em;
    &--search {
      padding-bottom: 1em;
    }
  }

  .catalog-top{
    &__row{
      height: 3.875em;
      width: calc(100% + 3.5em);
      margin-left: -1.75em;
      padding: 0 1.75em;
    }
    &__row2{
      margin: 0;
      padding: .625em 0 1.875em;
    }
    .sort{
      .nice-select{
        .list{
          left: unset;
          right: 0;
        }
      }
    }
  }

  .catalog-search{
    height: 3.875em;
  }

  .selected-filters{
    padding: 0;
    margin-top: -.625em;
    &__item{
      margin-right: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__nav{
      padding: .9375em 0;
      .m-btn{
        width: 100%;
      }
    }
  }

  .catalog-top{
    padding-bottom: 3.25em;
    margin-bottom: .9375em;
    position: relative;
    &__row{
      width: calc(100% + 1.875em);
      padding: 0 .9375em;
      margin-left: -.9375em;
    }
    &__row2{
      padding-bottom: 0;
    }
    &__filter-btn{
      margin-right: .9375em;
    }
    .sort{
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }

  .category-sort{
    &__title{
      display: none;
    }
    .nice-select{
      .current{
        color: $black;
      }
    }
  }

  .catalog-search{
    display: none;
  }
}
