.contacts {
  padding-bottom: 3.125em;
  .m-title--arrow svg {
    top: unset;
    bottom: 0;
    margin-left: -4em;
  }
  .about-top__desc {
    display: flex;
    align-items: flex-end;
    padding: 0 0 2.875em 0;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 6em / 4));
    gap: 2em;
    margin-top: 3.125em;
  }
  &__item {
    border: 0.1875em solid #EEEEEE;
    padding: 2rem;
    &-title {
      max-width: 16.25em;
      font-weight: 600;
      text-transform: uppercase;
    }
    &-socials {
      display: inline-flex;
      flex-direction: column;
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 1.5em;
    }
    &-social {
      display: flex;
      align-items: center;
      gap: 0.625em;
      color: #7C7C7C;
      font-weight: 500;
      &:hover {
        .social-logo svg path {
          fill: $primary;
        }
        .arrow svg path {
          stroke: $primary;
        }
      }
      p {
        font-size: 0.75em;
        line-height: 130%;
        text-transform: uppercase;
      }
    }
    .social-logo {
      width: 1.25em;
      max-width: 1.25em;
      flex-shrink: 0;
      svg {
        max-width: 100%;
        height: auto;
      }
    }
    .arrow {
      width: 0.5625em;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-phone {
      display: inline-block;
      font-size: 1.375em;
      line-height: 130%;
      font-weight: 600;
      color: #454545;
      margin-top: 1.09em;
    }
    &-mail {
      display: inline-block;
      font-size: 1.375em;
      line-height: 130%;
      font-weight: 600;
      color: #454545;
      border-bottom: 0.0625rem solid #454545;
      margin-top: 1.09em;
      &:hover {
        border-bottom: 0.0625em solid #ff6001;
      }
    }
    &-text {
      text-transform: uppercase;
      color: #8C8C8C;
      margin-top: 1.5em;
      line-height: 183%;
    }
  }
}

.connection {
  position: relative;
  background: #F5F5F5;
  padding: 5em 0 7.5em;
  overflow: hidden;
  .container {
    position: relative;
    z-index: 2;
  }
  &-form {
    max-width: 80em;
    display: grid;
    grid-template-columns: repeat(12, calc(100% / 12 - 22em / 12));
    gap: 2em;
    margin-top: 2em;
    &__btn {
      padding: 0 3.75em;
    }
    &__text{
      a{
        text-decoration: underline;
      }
    }
  }
  .fg {
    &:nth-child(1) {
      grid-column: 1/5;
    }
    &:nth-child(2) {
      grid-column: 5/9;
    }
    &:nth-child(3) {
      grid-column: 9/13;
    }
    &:nth-child(4) {
      grid-column: 1/6;
    }
    &:nth-child(5) {
      grid-column: 6/13;
    }
    &-flex {
      display: flex;
      align-items: center;
      gap: 2em;
    }
  }
  &-img {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 34.75em;
    pointer-events: none;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding-bottom: 1.875em;
    .m-title--arrow svg {
      margin-left: -3em;
    }
    .about-top__inner{
      padding-right: 9.875em;
    }
    .about-top__desc {
      padding-bottom: 0;
      max-width: 100%;
      br {
        display: none;
      }
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
      margin-top: 0;
    }
    &__item {
      padding: 1.0625em;
    }
    &__item {
      &-socials {
        flex-direction: row;
        margin-top: 1.25em;
      }
      &-title {
        font-size: 0.875em;
        line-height: 178%;
      }
      &-phone,
      &-mail {
        font-size: 1.1875em;
        margin-top: 1em;
      }
      &-text {
        margin-top: 1.67em;
        line-height: 158%;
      }
    }
  }
  .connection {
    padding: 1.875em 0;
    .h2 {
      text-align: center;
      font-size: 1.8125em;
    }
    &-form {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
    .fg {
      grid-column: unset !important;
      &:last-child {
        grid-column: 1/3 !important;
      }
      &-flex {
        gap: 1.25em;
      }
    }
    &-img {
      right: -17em;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    .about-top__inner{
      padding-right: 0;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__item {
      &-socials {
        flex-direction: column;
        gap: 1.25em;
      }
    }
    .m-arrows{
      top: 1.125em;
    }
  }
  .connection {
    &-form {
      grid-template-columns: 100%;
    }
    .fg {
      flex-direction: column;
      align-items: center;
      text-align: center;
      &:last-child {
        grid-column: unset !important;
      }
    }
    &-img {
      right: 0;
      max-width: 54.125em;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
}
