.gallery-modal{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  background-color: $white;
  z-index: 100;
  overflow-y: auto;
  padding: 3.125em;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._open{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: fixed;
    top: 3.125em;
    right: 3.125em;
    opacity: .4;
    width: 2.25em;
    background-color: transparent;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      opacity: 1;
    }
  }
  &__items{
    @include grid100gap(1.875em);
    width: 75em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__item{
    video,
    img{
      width: 100%;
      display: block;
    }
  }
}

.m-alert{
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 12.5em;
  right: 5.5em;
  width: 20.25em;
  box-shadow: 0 9px 20px 0 rgba(0, 0, 0, 0.07), 0 36px 36px 0 rgba(0, 0, 0, 0.06), 0 80px 48px 0 rgba(0, 0, 0, 0.04), 0 142px 57px 0 rgba(0, 0, 0, 0.01), 0 222px 62px 0 rgba(0, 0, 0, 0);
  &._open{
    opacity: 1;
    pointer-events: unset;
  }
}

.product-alert{
  display: grid;
  grid-template-columns: 5.5em calc(100% - 5.5em);
  &__img{
    @include imgCover(93.1%);
    min-height: 100%;
  }
  &__content{
    padding: 1.25em .9375em;
    align-self: center;
    background-color: $white;
  }
  &__desc{
    font-size: .875em;
    line-height: 120%;
    color: #646464;
  }
  &__title{
    font-size: 1.125em;
    line-height: 118%;
    margin-top: .28em;
  }
}

.m-alert2{
  .product-alert{
    grid-template-columns: 100%;
    &__content{
      padding: 1.5625em;
    }
  }
  @media screen and (min-width: 993px){
    width: 26.625em;
  }
}




@media screen and (max-width: 992px){
  .gallery-modal{
    padding: 2.1875em 3.5em;
    &__close{
      top: .625em;
      right: .625em;
      width: 1.5625em;
    }
    &__items{
      gap: .9375em;
    }
  }

  .m-alert{
    top: 3.125em;
    right: 3.125em;
  }
}


@media screen and (max-width: 576px){
  .gallery-modal{
    padding: 3.5em 1.25em 1em;
  }

  .m-alert{
    top: 8.125em;
    right: 1.125em;
    width: calc(100vw - 2.25em);
  }
}
