.running-line{
  padding: 1.375em .5em;
  border-bottom: 1px solid #cfcfcf;
  border-top: 1px solid #cfcfcf;
  overflow: hidden;
  color: #4a4a4a;
  &__inner{
    display: flex;
  }
  &__items{
    display: flex;
    animation: 30s slides infinite linear;
  }
  &__item{
    display: flex;
    white-space: nowrap;
    flex-shrink: 0;
    margin-right: 3.125em;
    text-transform: uppercase;
    &:after{
      content: '';
      flex-shrink: 0;
      width: .5625em;
      height: .75em;
      align-self: flex-end;
      background-image: url(../img/arrow-right4.svg);
      @include bgImgSize(100%);
      margin-left: 3.125em;
      margin-bottom: .25em;
    }
    &-title{
      font-size: 1em;
      line-height: 125%;
      font-weight: 600;
    }
    &-desc{
      font-size: .5625em;
      line-height: 122%;
      font-weight: 500;
      opacity: .6;
      margin-left: .33em;
    }
  }
}

@keyframes slides {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

.main{
  position: relative;
  background: radial-gradient(175.62% 168.18% at 51.95% 56.42%, rgb(255, 255, 255) 0%, rgb(217, 217, 217) 100%);
  margin-top: -5.25em;
  overflow: hidden;
  .swiper-slide-active{
    .main-banner__group,
    .main-banner__group2,
    .main-banner__img{
      opacity: 1;
    }
    .main-banner__inner:after{
      transform: translateY(0);
      opacity: 1;
    }
    .main-banner__group,
    .main-banner__group2{
      transform: translateX(0);
    }
  }
  .swiper-nav{
    bottom: 4.375em;
    right: 18em;
    position: absolute;
  }
  .swiper-scrollbar{
    position: absolute;
    z-index: 3;
    left: 17.75em;
    bottom: 4.375em;
    width: 12.5em;
    height: .25em;
    background: #e9e9e9;
    border-radius: 0;
    &-drag{
      background: $black;
      border-radius: 0;
    }
  }
}

.main-nav{
  z-index: 4;
  position: absolute;
  right: 4.375em;
  bottom: 4.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    &-link{
      width: 1.375em;
      @include center;
      margin-bottom: 1.625em;
      &:last-child{
        margin-bottom: 0;
      }
      g{
        transition: all .3s;
      }
      svg{
        max-width: 100%;
      }
      &:hover{
        path{
          fill: $primary;
          opacity: 1;
        }
        g{
          opacity: 1;
        }
      }
    }
  }
  &__text{
    font-weight: 500;
    color: #838383;
    margin-top: 2em;
    text-transform: uppercase;
    span{
      transform: rotate(-180deg);
      font-size: .6875em;
      writing-mode: tb-rl;
    }
    ._view2{
      display: none;
    }
  }
}

._white-header{
  .main-slider{
    .swiper-scrollbar{
      background-color: rgba(#E9E9E9,.21);
      &-drag{
        background-color: $white;
      }
    }
    .swiper-button{
      path{
        stroke: $white;
      }
      &:hover{
        border-color: $white;
      }
    }
  }
  .main-nav__socials-link{
    path{
      fill: #fff;
    }
  }
}

.main-banner{
  position: relative;
  padding: 9.5em 0 4.375em;
  &._white{
    color: $white;
    .main-banner__inner:after{
      display: none;
    }
  }
  &__inner:after,
  &__img,
  &__group,
  &__group2{
    opacity: 0;
    transition: all .4s;
  }
  &__inner{
    height: 43.75em;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      right: 24.5em;
      bottom: -4.375em;
      width: 5.125em;
      height: 29.5em;
      background-color: $secondary;
      transform: translateY(100%);
      transition-delay: .6s;
      z-index: 3;
    }
  }
  &__img{
    width: 50.5em;
    position: absolute;
    left: 20.625em;
    bottom: 0;
    z-index: 5;
    opacity: 0;
    transition-delay: .2s;
    img{
      width: 100%;
      display: block;
      filter:
              drop-shadow(8px 23px 54px rgba(0, 0, 0, 0.1))
              drop-shadow(30px 93px 98px rgba(0, 0, 0, 0.09))
              drop-shadow(68px 209px 132px rgba(0, 0, 0, 0.05))
              drop-shadow(122px 372px 156px rgba(0, 0, 0, 0.01))
    ;
    }
  }
  &__group{
    transition-delay: 1s;
    position: absolute;
    z-index: 6;
    top: 11.5em;
    left: 20.5em;
    transform: translateX(-100%);
    .h1{
      text-align: right;
      text-transform: uppercase;
    }
  }
  &__year{
    font-size: 1.375em;
    line-height: 105%;
  }
  &__group2{
    transition-delay: 1.4s;
    position: absolute;
    z-index: 4;
    right: 19em;
    bottom: 11.875em;
    transform: translateX(100%);
  }
  &__desc{
    max-width: 10em;
    margin: 1em 0 0 4em;
    .text19{
      line-height: 137%;
    }
  }
  &__bg{
    position: absolute;
    z-index: 2;
    width: 41.5em;
    bottom: 8em;
    left: 29em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__link{
    position: absolute;
    left: 17.75em;
    bottom: 11.5em;
    z-index: 6;
  }
  &__img2{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
}

.main-page{
  .header:before{
    display: none;
  }
}

.arrow-link{
  display: flex;
  align-items: center;
  font-weight: 500;
  text-transform: uppercase;
  &__rect{
    @include center;
    width: 4.375em;
    height: 4.375em;
    background-color: $primary;
    flex-shrink: 0;
    margin-right: 1em;
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      left: -50%;
      bottom: -50%;
      transition: all .9s;
      background-color: $secondary;
      border-radius: 50%;
    }
    img{
      position: relative;
      z-index: 2;
      width: 3.25em;
    }
  }
  &__text{
    font-size: .875em;
  }
  &:hover{
    color: inherit;
    .arrow-link__rect:before{
      width: 10em;
      height: 10em;
    }
  }
}


@media screen and (max-width: 992px){
  .running-line{
    padding: .5em;
    &__item{
      margin-right: .9375em;
      &-title{
        font-size: .6875em;
      }
      &:after{
        margin-left: .9375em;
        margin-bottom: .0625em;
      }
    }
  }

  .main{
    margin-top: -3.375em;
    .swiper-scrollbar{
      display: none;
    }
    .swiper-nav{
      right: 1.5625em;
      bottom: 1.5625em;
    }
  }

  .main-nav{
    right: 1.5em;
    bottom: unset;
    top: 8.125em;
    &__socials{
      &-link{
        width: 1em;
        margin-bottom: 1.25em;
      }
    }
    &__text{
      margin-top: .5em;
      span{
        font-size: .625em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }


  .main-banner{
    padding: 5.375em 0 0;
    &__inner{
      height: 26.375em;
      &:after{
        width: 3em;
        height: 11.25em;
        right: 8.75em;
        bottom: 0;
      }
    }
    &__img{
      width: 29.5em;
      left: 5.25em;
      bottom: 1.75em;
    }
    &__bg{
      width: 28.125em;
      left: 8.75em;
      bottom: 5.5em;
    }
    &__group{
      top: 5.125em;
      left: 4.375em;
    }
    &__year{
      font-size: .8125em;
      line-height: 138%;
    }
    .h1{
      font-size: 3.625em;
    }
    &__group2{
      bottom: 4.25em;
      right: 3.125em;
    }
    &__desc{
      margin: 0;
      max-width: 8em;
      .text19{
        line-height: 127%;
      }
    }
    &__link{
      left: 1.25em;
      bottom: 6.625em;
    }
  }

  .arrow-link{
    &__rect{
      width: 2.6875em;
      height: 2.6875em;
      margin-right: .625em;
      img{
        width: 2em;
      }
    }
    &__text{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    .swiper-nav{
      display: none;
    }
  }

  .main-nav{
    display: none;
  }

  .main-banner{
    &__inner{
      &:after{
        width: 2.375em;
        height: 14.375em;
        right: 1.25em;
      }
    }
    &__img{
      width: 19.375em;
      left: -2em;
      bottom: 8em;
    }
    &__bg{
      width: 18.125em;
      left: .75em;
      bottom: 10.5em;
    }
    &__group{
      top: 4.75em;
      left: 0;
    }
    .h1{
      font-size: 2.0625em;
    }
    &__group2{
      bottom: 9em;
      right: 0;
    }
    &__desc{
      margin-top: .5em;
    }
    &__link{
      left: .9375em;
      bottom: 3.125em;
    }
  }
}
