.blog-top{
  padding: 3.125em 0;
  &__inner{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.sort{
  display: flex;
  align-items: center;
  position: relative;
  &__icon{
    width: 1.375em;
    margin-right: .375em;
  }
  &__title{
    font-size: .9375em;
    line-height: 120%;
    font-weight: 500;
  }
  .nice-select{
    padding-left: 0;
    padding-right: 0;
    height: auto;
    border: none;
    position: static;
    &:after{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      right: 0;
      top: 0;
      transform: unset !important;
    }
    .current{
      display: none;
    }
    .option{
      display: flex !important;
    }
    .list{
      width: auto;
      min-width: 100%;
      margin-top: .375em;
    }
  }
}

.blog-content{
  &__nav{
    margin-top: 3.75em;
  }
}

.blog-group{
  column-count: 4;
  column-gap: 1.25em;
  .blog-card{
    display: inline-block;
    width: 100%;
    margin-bottom: 3.75em;
  }
  &--main{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
    column-count: unset;
    margin-bottom: 3.75em;
    .blog-card{
      margin: 0;
      &:first-child{
        @media screen and (min-width: 577px){
          grid-column: 1/3;
          .blog-card__content{
            margin-top: 1.5em;
            display: grid;
            grid-template-columns: 13.75em calc(100% - 14.75em);
            gap: 1em;
          }
          .blog-card__bottom{
            margin: 0;
          }
        }
      }
      &__subtitle{
        font-size: .875em;
        color: $black;
        font-weight: 600;
      }
      @media screen and (min-width: 993px){
        &__title{
          font-size: 1.25em;
          line-height: 125%;
          font-weight: 500;
        }
      }
    }
  }
}

.blog-card{
  &__img{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    margin-top: 1em;
  }
  &__subtitle{
    font-size: .6875em;
    line-height: 118%;
    color: #9E9E9E;
    text-transform: uppercase;
  }
  &__bottom{
    margin-top: 1em;
  }
  &__title{
    font-weight: 500;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .67em;
    font-size: .9375em;
    line-height: 140%;
    color: #656565;
  }
  &__nav{
    display: flex;
    margin-top: .625em;
  }
  &__link{
    font-size: .875em;
    line-height: 150%;
    font-weight: 500;
    border-bottom: 1px solid $black;
    &:hover{
      border-color: $primary;
      color: $primary;
    }
  }
}

.more-btn{
  padding: 2.5em 0;
  display: flex;
  justify-content: center;
  .m-btn{
    i{
      color: #5f5f5f;
      font-style: normal;
    }
    &:hover{
      i{
        color: inherit;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .blog-top{
    padding: 1.875em 0 .625em;
  }

  .more-btn{
    padding: 1.875em 0;
  }

  .blog-content{
    &__nav{
      margin-top: .875em;
      padding: 0;
    }
  }

  .blog-group{
    column-count: 2;
    .blog-card{
      margin-bottom: 1.875em;
    }
    &--main {
      grid-template-columns: repeat(2, calc(50% - .625em));
      margin-bottom: 1.875em;
    }
  }

  .blog-card{
    &__title{
      font-size: .9375em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .sort{
    @media screen and (min-width: 577px){
      .nice-select{
        .list{
          left: unset;
          right: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog-top{
    padding: 1.5625em 0 1.875em;
    &__inner{
      flex-direction: column;
      align-items: flex-start;
    }
    .sort{
      margin-top: .9375em;
    }
  }

  .blog-group{
    column-count: 1;
    .blog-card{
      margin-bottom: 1em;
    }
    &--main {
      grid-template-columns: 100%;
    }
  }
}
