.footer{
  padding: 3.125em 0;
  background-color: $black;
  color: $white;
  &__logo{
    display: block;
    width: 11.875em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__address,
  &__c{
    color: #9a9a9a;
    .text16{
      line-height: 137%;
    }
  }
  &__c{
    max-width: 23.5em;
  }
  &__address{
    max-width: 26em;
  }
}

.footer-top{
  display: flex;
  justify-content: space-between;
}

.footer-nav{
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: .5625em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 147%;
    letter-spacing: 0.03em;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .5625em);
    display: flex;
    align-items: flex-start;
    color: #9a9a9a;
    font-weight: 500;
    font-size: 1em;
    line-height: 137%;
  }
}

.footer-contacts{
  width: 14.5em;
  &__items{
    margin-top: .5625em;
  }
  &__group{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  &__phone{
    font-size: 1.3125em;
    line-height: 105%;
    font-weight: 500;
    color: #9a9a9a;
  }
  &__email{
    font-size: 1em;
    line-height: 137%;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    font-weight: 500;
    color: $primary;
    margin-top: .5625em;
  }
  &__warning{
    margin-top: .5625em;
    color: #7c7c7c;
    font-weight: 500;
    .text14{
      line-height: 120%;
    }
  }
}

.footer-socials{
  margin-top: .5625em;
  display: flex;
  &__item{
    display: flex;
    align-items: center;
    color: #989898;
    text-transform: uppercase;
    font-weight: 500;
    margin-right: 1.25em;
    &:last-child{
      margin-right: 0;
    }
    svg{
      width: 1.125em;
      margin-right: .3125em;
    }
    &._ig{
      width: .875em;
    }
    span{
      font-size: .8125em;
      line-height: 115%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.footer-bottom{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2.5em;
  padding-top: 2.25em;
  border-top: 1px solid rgba($white,.14);
}

.footer-pay{
  display: flex;
  align-items: center;
  &__item{
    margin-right: 3.125em;
    opacity: .4;
    transition: all .3s;
    &:hover{
      opacity: 1;
    }
    &:last-child{
      margin: 0;
    }
    img{
      max-height: 1.125em;
      max-width: 2.8125em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.875em 0;
    &__logo{
      margin-right: 2.5em;
    }
    &__address,
    &__c{
      max-width: unset;
      .text16{
        font-size: .875em;
        line-height: 129%;
      }
      br{
        display: none;
      }
    }
    &__c{
      width: calc(100% - 14.375em);
    }
    &__address{
      margin-top: 1.25em;
      width: 100%;
    }
  }

  .footer-top{
    flex-wrap: wrap;
  }

  .footer-nav{
    &__title{
      font-size: .875em;
      line-height: 157%;
    }
    &__link{
      @include lastM(0 0 .64em);
      font-size: .875em;
    }
  }

  .footer-contacts{
    width: 100%;
    @media screen and (min-width: 577px){
      padding: 1.25em 0;
      &__items{
        display: flex;
      }
      &__group{
        margin-right: 3.875em;
      }
      .footer-socials{
        margin: 0;
      }
    }
    &__email{
      margin: 0;
    }
  }

  .footer-bottom{
    flex-wrap: wrap;
    margin-top: 0;
    padding-top: 1.25em;
  }

  .footer-pay{
    justify-content: center;
    margin-top: 1.25em;
    width: 100%;
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 4.625em;
    &__logo{
      margin: 0;
    }
    &__c{
      width: 100%;
      margin-top: 1.25em;
    }
  }

  .footer-top{
    @include grid100gap(1.25em);
  }

  .footer-nav{
    width: 100%;
    &._active{
      .footer-nav__btn svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      padding-bottom: .1875em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgba($white,.14);
    }
    &__btn{
      @include center;
      width: 1em;
      height: 1em;
      margin-left: .25em;
      svg{
        width: .5em;
      }
    }
    &__hidden{
      display: none;
    }
  }

  .footer-contacts{
    padding-bottom: 1.25em;
    &__email{
      margin-top: .5em;
    }
  }

  .footer-bottom{
    flex-direction: column;
    flex-wrap: nowrap;
    text-align: center;
  }
}
