@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.75em;
    height: 3.4375em;
    font-weight: 600;
    line-height: 162%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    position: relative;
    &:focus {
        box-shadow: none;
    }
    span,
    svg,
    img{
        position: relative;
        z-index: 2;
    }
    span{
        font-size: .8125em;
    }
    svg,
    img{
        width: 1.125em;
        margin-left: .625em;
        margin-right: .625em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
    &:after{
        position: absolute;
        content: "";
        top: -1px;
        left: -1px;
        width: 0;
        height: calc(100% + 2px);
        background: $primary;
        transition: all .3s;
    }
    &:hover:after{
        width: calc(100% + 2px);
    }
}

.m-btn-small{
    height: 2.75em;
    span{
        font-size: .75em;
    }
}

.m-btn-arrow{
    svg,
    img{
        width: 2em;
    }
}

.m-btn-black{
    background-color: $black;
    color: $white;
    &:hover{
        color: $white;
    }
    &:disabled{
        background-color: rgba($black,.3);
        pointer-events: none;
    }
}

.m-btn-gray{
    background: #f5f5f5;
    color: $black;
    &:hover{
        color: $white;
    }
}

.m-btn-black-outline{
    color: $black;
    border: 1px solid $black;
    &:hover{
        color: $white;
        path{
            fill: $white;
        }
    }
    &:after{
        background-color: $black;
    }
}
