@keyframes flip-horizontal-bottom {
    0% {
        transform: rotateX(-180deg);
        opacity: 0;
    }
    100% {
        opacity: 1;
        transform: rotateX(0deg);
    }
}

.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .8;
}

.m-alert,
.modal{
    &__close{
        position: absolute;
        top: .1875em;
        right: .1875em;
        opacity: .4;
        width: .875em;
        background-color: transparent;
        @include center;
        svg{
            width: 100%;
        }
        &:hover{
            opacity: 1;
        }
    }
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
        .modal__dialog{
            animation: flip-horizontal-bottom 0.4s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
        }
    }
    &__dialog {
        background-color: $white;
        background-image: url(../img/modal-bg.svg);
        background-size: auto 100%;
        background-repeat: no-repeat;
        background-position: right top;
        width: 65.25em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        padding: 3.125em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
    }
    &__close {
        top: 1.875em;
        right: 1.875em;
    }
    &__desc{
        max-width: 20em;
        padding-right: 3.75em;
    }
    &__arrows{
        position: absolute;
        right: 3.125em;
        bottom: 3.125em;
        width: 6.375em;
        overflow: hidden;
    }
}

.modal-form{
    margin-top: 3.125em;
    &__fields{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 3.125em / 3));
        gap: 1.5625em;
    }
    .file-input{
        margin-top: 1.5625em;
    }
    &__bottom{
        margin-top: 3.125em;
    }
    &__btn{
        padding: 0 3.75em;
        margin-right: 1.25em;
    }
    &__hint{
        max-width: 30em;
        color: #727272;
        a{
            text-decoration: underline;
        }
        .text14{
            line-height: 150%;
        }
    }
}

._full{
    grid-column: 1/-1;
}

.thank-modal{
    .modal__dialog{
        width: 33.25em;
        background-image: url(../img/modal-bg3.svg);
    }
    &__desc{
        margin-top: 3.125em;
        color: #343434;
    }
    &__btn{
        margin-top: 3.125em;
        width: 12.5em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            background-image: url(../img/modal-bg2.svg);
            width: 41.375em;
            padding: 1.5625em;
        }
        &__top{
            @include grid100gap(.5em);
            max-width: 16.875em;
        }
        &__desc{
            padding-right: 0;
            max-width: 16.25em;
        }
        &__arrows{
            right: 1.5625em;
            top: 4.75em;
            width: 9.5em;
        }
    }

    ._full-tablet{
        grid-column: 1/-1;
    }

    .modal-form{
        margin-top: 1.5625em;
        &__fields{
            grid-template-columns: repeat(2,calc(50% - 1.5625em / 2));
        }
        &__bottom{
            flex-direction: column;
            margin-top: 1.5625em;
        }
        &__btn{
            margin: 0 0 .875em;
        }
        &__hint{
            max-width: 100%;
            text-align: center;
        }
    }

    .thank-modal{
        .modal__dialog{
            width: 20.625em;
            padding: 2.1875em;
        }
        &__desc{
            margin-top: 1.5625em;
        }
        &__btn{
            margin-top: 1.5625em;
        }
        .modal__arrows{
            display: none;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: .9375em;
        }
        &__arrows{
            display: none;
        }
        &__close{
            top: 1.25em;
            right: 1.25em;
        }
    }

    .modal-form{
        &__fields{
            grid-template-columns: 100%;
        }
    }
}
