.catalog-items{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - .9375em));
  gap: 1.25em;
  grid-auto-flow: dense;
  ._big{
    grid-column: span 2;
    grid-row: span 2;
  }
}

.catalog-slider{
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

._open-filter{
  .filter{
    opacity: 1;
    pointer-events: unset;
    &__content{
      transform: translateX(0);
    }
  }
}

.filter{
  z-index: 100;
  &__content{
    padding: 5em 1.875em;
  }
  &__groups{
    @include grid100gap(2.5em);
  }
  &__nav{
    margin-top: 1.125em;
  }
  &__btn{
    width: 100%;
  }
}

.filter-group{
  &__top{
    margin-bottom: .75em;
    color: #5A5A5A;
  }
  &__checks{
    @include grid100gap(.9375em);
    @include scroll;
    &-nav{
      margin-top: 1.125em;
      ._view2{
        display: none;
      }
    }
  }
  &:not(._open){
    .filter-group__checks{
      max-height: 14.5em;
      overflow-y: auto;
    }
  }
  &._open{
    .filter-group__checks-more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.line-btn{
  font-weight: 500;
  border-bottom: 1px dashed $black;
  span{
    font-size: .9375em;
    line-height: 140%;
  }
}

.catalog-banner{
  display: block;
  grid-column: span 2;
  grid-row: span 2;
  position: relative;
  color: $white;
  &:hover{
    color: $white;
  }
  &._type2{
    grid-row: unset;
    .catalog-banner__img{
      padding-bottom: 55%;
    }
  }
  &__img{
    min-height: 100%;
    @include imgCover(100%);
    &:after{
      background: rgba(0,0,0,.22);
    }
  }
  &__content{
    width: 100%;
    @include centerAbsolute;
    padding: 9.5em;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    .m-badge{
      padding: 0 .625em;
      height: 3em;
      font-family: $font2;
      font-weight: 600;
      text-transform: unset;
      margin-top: 1.75em;
      span{
        font-size: 1.375em;
      }
    }
  }
  &__title{
    text-transform: uppercase;
    @media screen and (min-width: 993px){
      font-weight: 500;
      font-size: 4.25em;
      line-height: 108%;
    }
  }
  &__bottom{
    position: absolute;
    z-index: 2;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.25em;
  }
  &__title2{
    font-size: .9375em;
    line-height: 140%;
    text-transform: uppercase;
  }
  &__btn{
    width: 1.75em;
    @include center;
    img{
      width: 100%;
    }
  }
  &__link{
    position: absolute;
    bottom: 5.5em;
    left: 5em;
  }
}


@media screen and (max-width: 992px){
  .catalog-items{
    .product-card{
      @media screen and (min-width: 577px){
        &__title{
          font-size: .75em;
        }
      }
    }
  }

  .catalog-collection{
    padding: 1.875em 0;
    .m-title{
      max-width: 12em;
    }
  }

  .catalog-slider{
    .swiper{
      &-container{
        width: calc(100% + .9375em);
      }
      &-slide{
        padding-right: .9375em;
      }
    }
  }

  .filter{
    &__content{
      padding: 2.5em 1.875em;
    }
  }

  .filter-group{
    &__checks{
      padding-right: 1.25em;
      width: calc(100% + 1.25em);
    }
  }

  .catalog-banner{
    &__content{
      padding: 2em;
    }
    .m-badge{
      margin-top: .5em;
      height: 2.125em;
      span{
        font-size: 1em;
      }
    }
    &__bottom{
      padding: .625em;
    }
    &__title2{
      font-size: .8125em;
    }
    &__link{
      bottom: 1.6875em;
      left: 1em;
      padding: 0 .8125em;
      height: 2.8125em;
      img{
        display: none;
      }
      span{
        font-size: .6875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-items{
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    gap: .9375em;
  }

  .catalog-collection{
    overflow: hidden;
    &__top{
      max-width: 78%;
    }
  }

  .catalog-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        padding-right: .625em;
        width: 19.375em;
      }
    }
  }

  .catalog-banner{
    &__img{
      padding-bottom: 117%;
    }
    &__link{
      display: none;
    }
  }
}

